class SidemenuController {
  constructor(LoginSrv, $rootScope, constants, $window) {
    'ngInject';
    this.LoginSrv = LoginSrv;
    this.$window = $window;
    this.$rootScope = $rootScope;
    this.constants = constants;
    // this.tab == 'dashboard';

    this.intialServices();

  }
  intialServices() {
    this.LoginSrv.getUser((data) => {
      this.user = data;
    })
    this.LoginSrv.getUserphoto(this.user, (data) => {
      if(data == null) {
        this.$rootScope.photo = window.location.origin+'/assets/img/default_image.jpg';
      } else {
        this.$rootScope.photo = this.constants.EXPORT_URL+data;
      }

    })
  }

  logout() {
    this.LoginSrv.logout();
  }
}

export default SidemenuController;



