class TemplatesController {
  constructor(TemplateSrv, ToastSrv, $uibModal, $rootScope, blockUI, blockUIConfig) {
    'ngInject';

    this.name = 'Templates';

    this.TemplateSrv = TemplateSrv;
    this.ToastSrv = ToastSrv;
    this.$uibModal = $uibModal;
    this.$rootScope = $rootScope;

    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.myBlockUI = this.blockUI.instances.get('myBlockUI');
    this.blockUIConfig.resetOnException = true;

    this.addView = false;
    this.editView = true;

    this.maxSize = 3; //pagination  intial values
    this.pageItems = 12;
    this.CurrentPage = 1;


    $rootScope.cancelFrom = this.cancelFrom.bind(this);
    $rootScope.intialServices = this.intialServices.bind(this);
    this.myBlockUI.start();
    this.intialServices();
  }

  cancelFrom() {
    this.addView = false;
    this.editView = true;
    this.addTemplatesubject = '';
    this.addTemplateName = '';
    this.addTemplatemessage = '';
  }

  intialServices() {
    this.templateSearchSelected = '';
    this.TemplateSrv.getTemplateSrv(this.CurrentPage, (resp) => {
      this.myBlockUI.stop();
      this.data = resp.data.results;
      this.totalItems = resp.data.count;
    })
  }

  pageChanged() {
    this.myBlockUI.start(); //pagination  value changes,
    this.CurrentPage = this.CurrentPage;
    this.intialServices();
  };

  createNewTemplate() {
    var addNewTemplate = {
      level: this.addTemplateName,
      subject: this.addTemplatesubject,
      message: this.addTemplatemessage,

    }
    this.myBlockUI.start();
    this.TemplateSrv.createTemplateSrv(addNewTemplate, (resp) => {
      if (resp.data.success) {
        this.ToastSrv.notify('Template Created successfully', 'success');
        this.intialServices();
        this.cancelFrom()
      } else {
        this.ToastSrv.notify('Template with same name already exists...!', 'error');
        this.myBlockUI.stop();
      }
    })
  }

  addVote(data) {
    data.vote = data.vote + 1;
    this.TemplateSrv.editTemplateSrv(data, (resp) => {
      this.blockUI.stop();
    })
  }

  minusVote(data) {
    data.vote = data.vote - 1;
    this.TemplateSrv.editTemplateSrv(data, (resp) => {
      this.blockUI.stop();
    })
  }

  getSearchTemplate(val) { //search in template 
    return this.TemplateSrv.getSearchTemplateSrv(val).then((result) => {
      this.searchResult = result;
      return result.map(function (item) {
        return item;
      });
    })
  }

  onSearchtemplate(searchResult) { //on select searchContacts 
    this.data = [searchResult];
  }

  cancelSearch() { //on cancel search Contacts 
    this.intialServices();
    this.templateSearchSelected = '';
    this.noResults='';
  }


  // *************//Modal Functions//**************//

  openEditModal(data) {

    this.$uibModal.open({
      templateUrl: 'editModal.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'blockUI', 'blockUIConfig', 'TemplateSrv', 'ToastSrv', function ($uibModalInstance, $rootScope, blockUI, blockUIConfig, TemplateSrv, ToastSrv) {
        this.blockUI = blockUI;
        this.editdata = data;
        this.blockUIConfig = blockUIConfig;
        this.TemplateSrv = TemplateSrv;
        this.ToastSrv = ToastSrv;


        this.id = this.editdata.id;
        this.editTemplateName = this.editdata.level;
        this.editTemplatesubject = this.editdata.subject;
        this.editTemplatemessage = this.editdata.message;

        this.editTemplate = function () {
          var editTemplate = {
            id: this.id,
            level: this.editTemplateName,
            subject: this.editTemplatesubject,
            message: this.editTemplatemessage,
          }
          this.blockUI.start();
          this.TemplateSrv.editTemplateSrv(editTemplate, (resp) => {
            this.blockUI.stop();
            if (resp.success) {
              $rootScope.intialServices();
              this.ToastSrv.notify('Template Updated successfully.', 'success');
              $rootScope.cancelFrom();
              $uibModalInstance.close();
            }
            else {
              this.ToastSrv.notify('Template Failed to update.Please try again..!', 'error');
              
            }
          })
        }

        this.close = function () {
          $uibModalInstance.close();
        }

      }],
      controllerAs: 'vt'
    })
  }

  openDeleteModal(data) {
    this.$uibModal.open({
      templateUrl: 'deleteModal.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'blockUI', 'blockUIConfig', 'TemplateSrv', 'ToastSrv', function ($uibModalInstance, $rootScope, blockUI, blockUIConfig, TemplateSrv, ToastSrv) {
        this.blockUI = blockUI;
        this.editdata = data;
        this.blockUIConfig = blockUIConfig;
        this.TemplateSrv = TemplateSrv;
        this.ToastSrv = ToastSrv;

        this.name = data.level;
        this.deleteTemplate = function () {
          var id = data.id;
          this.blockUI.start();
          this.TemplateSrv.deleteTemplateSrv(id, (resp) => {
            if (resp.status < 300 || resp.success) {
              this.ToastSrv.notify('Template deleted successfully.', 'success');
              $rootScope.cancelFrom();
              $rootScope.intialServices();
              this.blockUI.stop();
              $uibModalInstance.close();
            }
          })
        }

        this.close = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vu'
    })
  }

}

export default TemplatesController;



