class UsersController {
  constructor($uibModal, UserSrv, ToastSrv, moment, $rootScope, LoginSrv, constants, $window, blockUI, blockUIConfig) {
    'ngInject';

    this.UserSrv = UserSrv;
    this.LoginSrv = LoginSrv;
    this.ToastSrv = ToastSrv;
    this.constants = constants;
    this.$window = $window;
    this.addUser = false;
    this.editUser = false;
    this.showError = false;
    this.submiting = false; // variable for showing add user spinner
    this.updating = false; // variable for showing updating spinner
    this.phoneError = false; // variable for showing phone validation error
    this.moment = moment;
    this.$uibModal = $uibModal;
    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.myBlockUI = this.blockUI.instances.get('myBlockUI');
    this.blockUIConfig.resetOnException = true;


    this.options = { //datepicker settings
      maxDate: new Date(),
      showWeeks: false
    };

    this.tab = 1;
    this.cancelForm = this.cancelForm.bind(this); //For accessing function from another
    this.getUsers = this.getUsers.bind(this); //For accessing function from another
    $rootScope.getCities = this.getCities.bind(this); // For accessing inside modals
    $rootScope.getUsers = this.getUsers.bind(this); // For accessing inside modals
    $rootScope.UserSrv = this.UserSrv;
    $rootScope.ToastSrv = ToastSrv;

    this.UserSrv.getUsers((data) => { //getting all users from DB
      this.users = data;

    })

    this.LoginSrv.getUser((data) => {
      this.userData = data;
    })

    this.UserSrv.getAllCities((data) => { //Getting all cities from DB
      this.cities = data;
    })

    this.user = {
      address_details: {}
    };

    this.states = this.constants.STATES;
    $rootScope.states = this.states;

  }

  getUsers() {
    this.UserSrv.getUsers((data) => {
      this.users = data;
      this.userSearchSelected = '';
    })
  }

  getCities() {
    this.UserSrv.getAllCities((data) => {
      this.cities = data;
    })
  }
  removeCities(id) {
    this.UserSrv.removeCitiesSrv(id, (resp) => {
      if (resp.status < 300 || resp.success == true) {
        this.getCities();
        this.ToastSrv.notify('City Deleted sucessfully', 'success');
      } else {
        this.ToastSrv.notify('City Deletion Failed', 'error');
        this.getCities();

      }
    })
  }

  adduser() {
    this.addUser = true;
  }

  editUser() {
    this.editUser = true;
  }

  changeIsActive(val) {
    this.edit_user.is_active = val;
  }

  open1() { //open datetime picker inside modal 
    this.popup1 = true;
  };

  sortUsers(key, tab) { //Tab Function to sort users
    this.myBlockUI.start();
    this.tab = tab;
    this.UserSrv.sortUsers(key, (data) => {
      this.myBlockUI.stop();
      this.users = data;
    })
  }

  saveUser() { // Function to save new USER
    delete this.user.photo
    this.user.is_active = true;
     this.blockUI.start();
      
    this.user.date_of_birth = this.user.date_of_birth ? this.moment(this.user.date_of_birth).toISOString() : null;
    setTimeout(() => { //Due to Asynchronus execution
      this.UserSrv.saveNewUser(this.user, (resp) => {
        this.blockUI.stop();
        if (resp.success) {
          this.submiting = false;
          this.ToastSrv.notify('The user has been added successfully.', 'success')
          this.cancelForm();
        } else {
          this.submiting = false;
          this.ToastSrv.notify('User creation failed..!  ' + resp.msg.email[0] + '...!', 'error')
        }
      })
    }, 10);
  }

  editUsers(user) { //TO show edit user
    this.editUser = true;
    this.edit_user = user;
    if (this.edit_user.date_of_birth) {
      var test = user.date_of_birth.substring(0, 10);
      this.edit_user.date_of_birth = new Date(user.date_of_birth);
    }
    
  }

  editUserData() { //Function to update user
    this.showError = false;
    this.phoneError = false;
    delete this.edit_user.photo;
    this.edit_user.date_of_birth = this.edit_user.date_of_birth ? this.moment(this.edit_user.date_of_birth).toISOString(): '';
    // if(this.edit_user.date_of_birth) {this.edit_user.date_of_birth = this.edit_user.date_of_birth.split("-").reverse().join("-");}
    if (this.edit_user.address_details) {
      delete this.edit_user.address_details.user;
    } else {
      delete this.edit_user.address_details
    }
    this.myBlockUI.start();
    this.UserSrv.editUser(this.edit_user, (resp) => {
      this.myBlockUI.stop();
      this.updating = false;
      if (resp.success) {
        this.ToastSrv.notify('The user has been updated successfully.', 'success')
        // this.getUsers();
        this.cancelForm();
      } else {
        this.ToastSrv.notify('Failed to update user', 'error')
      }
    })
  }

  noDelete(user) {
    this.ToastSrv.notify(user.full_name + ' is assigned to a Property so deletion is not possible', 'error')
  }

  getSearchUser(val) { //search in Users 
    return this.UserSrv.getSearchUserSrv(val).then((result) => {
      this.searchResult = result;
      return result.map(function (item) {
        return item;
      });
    })
  }

  onSearchUser(searchResult) { //on select searchUsers 
    this.tab = 1;
    this.users = [searchResult];
  }

  cancelForm() { //TO cancel edit and create user forms
    this.addUser = false;
    this.editUser = false;
    this.user = {};
    this.edit_user = {};
    this.cancelSearch();
  }

  cancelSearch() { //on cancel search Users 
    this.tab = 1;
    this.getUsers()
    this.userSearchSelected = '';

  }

  // Modal Functions
  openCityModal() { //function for opening add city
    this.$uibModal.open({
      templateUrl: 'city.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        this.states = $rootScope.states; //this.states - only accessable in modal
        this.data = {
          "deletable": true
        }; // this.data - - only accessable in modal
        this.close = function () {
          $uibModalInstance.close();
        };
        this.addCity = function () {
          $rootScope.UserSrv.saveCity(this.data, (resp) => {
            if (resp.success) {
              $rootScope.ToastSrv.notify("City has been updated Successfully.", 'success');
              $rootScope.getCities();
              $uibModalInstance.close();
            } else {
              $rootScope.ToastSrv.notify("Failed to add city...! City with selected state already exists..!", 'error');
            }
          })
        }
      }],
      controllerAs: 'vm'
    })
  }

  deleteUserModal(usr) { //modal to show delete user warning
    this.$uibModal.open({
      templateUrl: 'deleteuser.html',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        this.user = usr; //only accessable in modal
        this.delete = function () {
          $rootScope.UserSrv.deleteUser(this.user, (resp) => {
            $rootScope.ToastSrv.notify("User has been deleted Successfully.", 'success');
            $rootScope.getUsers();
            $uibModalInstance.close();
          })
        }
        this.close = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vx'
    })
  }

}

export default UsersController;



