  getUnGroupedRecords(data, callback) {
    var status = ""
    if(data.status) {
      for(var i in data.status) {
        status = status.concat("&status="+data.status[i]) }
    } else {
      status = "&status="
    }
    const url = this.constants.BASE_URL + '/properties/records/?page='+data.page+'&qu=togroup&q='+data.date+'&from_date='+data.from+'&to_date='+data.to+'&str='+data.search+status+'&file='+data.file;
    return this.$http.get(url).then((resp) => {
      callback(resp.data);
    });
  }

