class ForgotPasswordController {
  constructor($state, LoginSrv, $rootScope, $uibModal) {
    'ngInject';
    this.$state = $state;
    this.$uibModal=$uibModal;
    this.LoginSrv = LoginSrv;
    this.$rootScope = $rootScope;
    this.emailErr=false;
  }


  forgotPassword() {
    var data = {
      email: this.emailaddress,
    }
    this.LoginSrv.forgotPassword(data, (response) => {
      var message = response.data.msg;
      if (response.data.success == true) {
        // modal function for forgotPassword password success start;
        this.$uibModal.open({
          size: 'sm',
          templateUrl: 'renewPassword.html',
          controller: ['$uibModalInstance', '$rootScope', '$state', function ($uibModalInstance, $rootScope, $state) {
            this.message=message;
            this.close = function () {
              $rootScope.$state.go('login')
              $uibModalInstance.close();


            }
          }],
          controllerAs: 'vm'

        });
      }
      else{
        this.emailErr=true;
        this.message=message;
      }
    })
  }

}

export default ForgotPasswordController;



