
// 'use strict';
import angular from 'angular';
//Directive used to set metisMenu and minimalize button
let sample = angular.module('sample', [])
    .directive('sideNavigation', ($timeout) => {
      "ngInject";
        return {
            restrict: 'A',
            link: (scope, element) => {
                // Call metsi to build when user signup
                scope.$watch('authentication.user', () => {
                    $timeout(() => {
                        element.metisMenu();
                    });
                });

            }
        };
    })
    .directive('minimalizaSidebar', ($timeout) => {
      "ngInject";
        return {
            restrict: 'A',
            template: '<a class="navbar-minimalize minimalize-styl-2 btn btn-primary " href="" ng-click="minimalize()"><i class="fa fa-bars"></i></a>',
            controller: ($scope) => {
                "ngInject";
                angular.element('body').toggleClass('mini-navbar');
                $scope.minimalize = () => {
                    angular.element('body').toggleClass('mini-navbar');
                    if (!angular.element('body').hasClass('mini-navbar') || angular.element('body').hasClass('body-small')) {
                        // Hide menu in order to smoothly turn on when maximize menu
                        angular.element('#side-menu').hide();
                        // For smoothly turn on menu
                        $timeout(() => {
                            angular.element('#side-menu').fadeIn(400);
                        }, 200);
                    } else {
                        // Remove all inline style from jquery fadeIn function to reset menu state
                        angular.element('#side-menu').removeAttr('style');
                    }
                };
            }
        };
    })
    .directive('phonenumberDirective', ['$filter', function($filter) {
        /*
        Intended use:
          <phonenumber-directive placeholder='prompt' model='someModel.phonenumber'></phonenumber-directive>
        Where:
          someModel.phonenumber: {String} value which to bind only the numeric characters [0-9] entered
            ie, if user enters 617-2223333, value of 6172223333 will be bound to model
          prompt: {String} text to keep in placeholder when no numeric input entered
        */
      
        function link(scope, element, attributes) {
      
          // scope.inputValue is the value of input element used in template
          scope.inputValue = scope.phonenumberModel;
      
          scope.$watch('inputValue', function(value, oldValue) {
            
            value = String(value);
            var number = value.replace(/[^0-9]+/g, '');
            scope.phonenumberModel = number;
            scope.inputValue = $filter('phonenumber')(number);
          });
        }
        
        return {
          link: link,
          restrict: 'E',
          scope: {
            phonenumberPlaceholder: '=placeholder',
            phonenumberModel: '=model',
          },
          //templateUrl: '/static/phonenumberModule/template.html',
          template: '<input ng-model="inputValue" type="tel" class="form-control input-sm" name="phonenumber" placeholder=" (999) 999-9999" title="Phonenumber (Format: (999) 9999-9999)">',
        };
      }])
      
      .filter('phonenumber', function() {
          /* 
          Format phonenumber as: c (xxx) xxx-xxxx
            or as close as possible if phonenumber length is not 10
            if c is not '1' (country code not USA), does not use country code
          */
          
          return function (number) {
            /* 
            @param {Number | String} number - Number that will be formatted as telephone number
            Returns formatted number: (###) ###-####
              if number.length < 4: ###
              else if number.length < 7: (###) ###
      
            Does not handle country codes that are not '1' (USA)
            */
              if (!number) { return ''; }
      
              number = String(number);
      
              // Will return formattedNumber. 
              // If phonenumber isn't longer than an area code, just show number
              var formattedNumber = number;
      
          // if the first character is '1', strip it out and add it back
          var c = '';
      
          // # (###) ###-#### as c (area) front-end
          var area = number.substring(0,3);
          var front = number.substring(3, 6);
          var end = number.substring(6, 10);
      
          if (front) {
            formattedNumber = (c + "(" + area + ") " + front);	
          }
          if (end) {
            formattedNumber += ("-" + end);
          }
          return formattedNumber;
          };
      })

      .filter('propsFilter', function() {
        return function(items, props) {
          var out = [];
      
          if (angular.isArray(items)) {
            var keys = Object.keys(props);
      
            items.forEach(function(item) {
              var itemMatches = false;
      
              for (var i = 0; i < keys.length; i++) {
                var prop = keys[i];
                var text = props[prop].toLowerCase();
                if(item[prop] != null) {
                  if (item[prop].toString().toLowerCase().indexOf(text) !== -1) {
                    itemMatches = true;
                    break;
                  }
                }
              }
      
              if (itemMatches) {
                out.push(item);
              }
            });
          } else {
            // Let the output be the input untouched
            out = items;
          }
      
          return out;
        };
      })
      
      .directive('autogrow', function () {
        return {
    
          restrict: 'A',
          link: function postLink(scope, element, attrs) {
              // hidding the scroll of textarea
              element.css('overflow', 'hidden');
    
              var update = function(){
    
                //  element.css("height", "auto");
    
                  var height = element[0].scrollHeight;
    
                  if(height > 0){
    
                      element.css("height", height + "px");
                    }
    
              };
    
              scope.$watch(attrs.ngModel, function(){
    
                  update();
              });
    
              attrs.$set("ngTrim", "false");
          }
        };
      })

      .filter('capitalize', function() {
        return function(input) {
          return (!!input) ? input.charAt(0).toUpperCase() + input.substr(1).toLowerCase() : '';
        }
    })
      .directive('currencyMask', function() {
        return {
          restrict: 'A',
          require: 'ngModel',
          link: function(scope, element, attrs, ngModelController) {
            
            var formatNumber = function(value) {
           
              value = value.toString();
              value = value.replace(/[^0-9\.]/g, "");
              var parts = value.split('.');
              parts[0] = parts[0].replace(/\d{1,3}(?=(\d{3})+(?!\d))/g, "$&,");
              if (parts[1] && parts[1].length > 2) {
                parts[1] = parts[1].substring(0, 2);
              }
             
              return parts.join(".");
            };
            var applyFormatting = function() {
              var value = element.val();
              var original = value;
              if (!value || value.length == 0) {
                return
              }
              value = formatNumber(value);
              if (value != original) {
                element.val(value);
                element.triggerHandler('input')
              }
            };
            element.bind('keyup', function(e) {
              var keycode = e.keyCode;
              var isTextInputKey =
                (keycode > 47 && keycode < 58) || // number keys
                keycode == 32 || keycode == 8 || // spacebar or backspace
                (keycode > 64 && keycode < 91) || // letter keys
                (keycode > 95 && keycode < 112) || // numpad keys
                (keycode > 185 && keycode < 193) || // ;=,-./` (in order)
                (keycode > 218 && keycode < 223); // [\]' (in order)
              if (isTextInputKey) {
                applyFormatting();
              }
            });
            // element.bind('blur', function(evt) {
            //   if (angular.isDefined(ngModelController.$modelValue)) {
            //     var val = ngModelController.$modelValue;
            //     if (val && val.length == 1) {
            //       if (val != "") {
            //         ngModelController.$setViewValue(val + '.00');
            //         ngModelController.$render();
            //       }
            //     } else if (val && val.length == 2) {
            //       if (val[1] && val[1].length == 1) {
            //         ngModelController.$setViewValue(val[0] + '.' + val[1] + '0');
            //         ngModelController.$render();
            //       } else if (val[1].length == 0) {
            //         ngModelController.$setViewValue(val[0] + '.00');
            //         ngModelController.$render();
            //       }
            //       applyFormatting();
            //     }
            //   }
            // })
            ngModelController.$parsers.push(function(value) {
              if (!value || value.length == 0) {
                return value;
              }
              value = value.toString();
              value = value.replace(/[^0-9\.]/g, "");
              return value;
            });
            ngModelController.$formatters.push(function(value) {
              if (!value || value.length == 0) {
                return value;
              }
              value = formatNumber(value);
              return value;
            });
          }
        };
      })
      .directive('wmBlock', function () {
        return {
            scope: {
              wmBlockLength: '='
            },
            link: function (scope, elm, attrs) {
             
              elm.bind('keypress', function(e){
               
                if(elm[0].value.length > scope.wmBlockLength){
                  e.preventDefault();
                  return false;
                }
              });
            }
        }   
    })
    .directive('angularTrix', function() {
      return {
          restrict: 'A',
          require: 'ngModel',
          scope: {
              trixInitialize: '&',
              trixChange: '&',
              trixSelectionChange: '&',
              trixFocus: '&',
              trixBlur: '&',
              trixFileAccept: '&',
              trixAttachmentAdd: '&',
              trixAttachmentRemove: '&'
          },
          link: function(scope, element, attrs, ngModel) {

              element.on('trix-initialize', function() {
                  if (ngModel.$modelValue) {
                      element[0].editor.loadHTML(ngModel.$modelValue);
                  }
              });

              ngModel.$render = function() {
                  if (element[0].editor) {
                      element[0].editor.loadHTML(ngModel.$modelValue);
                  }

                  element.on('trix-change', function() {
                      ngModel.$setViewValue(element.html());
                  });
              };

              var registerEvents = function(type, method) {
                  element[0].addEventListener(type, function(e) {
                      if (type === 'trix-file-accept' && attrs.preventTrixFileAccept === 'true') {
                          e.preventDefault();
                      }

                      scope[method]({
                          e: e,
                          editor: element[0].editor
                      });
                  });
              };

              registerEvents('trix-initialize', 'trixInitialize');
              registerEvents('trix-change', 'trixChange');
              registerEvents('trix-selection-change', 'trixSelectionChange');
              registerEvents('trix-focus', 'trixFocus');
              registerEvents('trix-blur', 'trixBlur');
              registerEvents('trix-file-accept', 'trixFileAccept');
              registerEvents('trix-attachment-add', 'trixAttachmentAdd');
              registerEvents('trix-attachment-remove', 'trixAttachmentRemove');

          }
      };
  })

  .directive('phoneInput', ['$filter', '$browser', function($filter, $browser) {
    return {
        require: 'ngModel',
        link: function($scope, $element, $attrs, ngModelCtrl) {
          var listener = function() {
            var value = $element.val().replace(/[^0-9]/g, '');
            $element.val($filter('tel')(value, false));
        };

        // This runs when we update the text field
        ngModelCtrl.$parsers.push(function(viewValue) {
            return viewValue.replace(/[^0-9]/g, '').slice(0,10);
        });

        // This runs when the model gets updated on the scope directly and keeps our view in sync
        ngModelCtrl.$render = function() {
            $element.val($filter('tel')(ngModelCtrl.$viewValue, false));
        };

        $element.bind('change', listener);
        $element.bind('keydown', function(event) {
            var key = event.keyCode;
            // If the keys include the CTRL, SHIFT, ALT, or META keys, or the arrow keys, do nothing.
            // This lets us support copy and paste too
            if (key == 91 || (15 < key && key < 19) || (37 <= key && key <= 40)){
                return;
            }
            $browser.defer(listener); // Have to do this or changes don't get picked up properly
        });

        $element.bind('paste cut', function() {
            $browser.defer(listener);
        });
    }

    };
}])

.filter('tel', function () {
  return function (number) {
    /* 
    @param {Number | String} number - Number that will be formatted as telephone number
    Returns formatted number: (###) ###-####
      if number.length < 4: ###
      else if number.length < 7: (###) ###

    Does not handle country codes that are not '1' (USA)
    */
      if (!number) { return ''; }

      number = String(number);

      // Will return formattedNumber. 
      // If phonenumber isn't longer than an area code, just show number
      var formattedNumber = number;

  // if the first character is '1', strip it out and add it back
  var c = '';

  // # (###) ###-#### as c (area) front-end
  var area = number.substring(0,3);
  var front = number.substring(3, 6);
  var end = number.substring(6, 10);

  if (front) {
    formattedNumber = (c + "(" + area + ") " + front);	
  }
  if (end) {
    formattedNumber += ("-" + end);
  }
  return formattedNumber;
  };
})
.directive('socialInput', ['$filter', '$browser', function($filter, $browser) {
  return {
      require: 'ngModel',
      link: function($scope, $element, $attrs, ngModelCtrl) {
          var listener = function() {
              var value = $element.val().replace(/[^0-9]/g, '');
              $element.val($filter('soc')(value, false));
          };

          // This runs when we update the text field
          ngModelCtrl.$parsers.push(function(viewValue) {
              return viewValue.replace(/[^0-9]/g, '').slice(0,9);
          });

          // This runs when the model gets updated on the scope directly and keeps our view in sync
          ngModelCtrl.$render = function() {
              $element.val($filter('soc')(ngModelCtrl.$viewValue, false));
          };

          $element.bind('change', listener);
          $element.bind('keydown', function(event) {
              var key = event.keyCode;
              // If the keys include the CTRL, SHIFT, ALT, or META keys, or the arrow keys, do nothing.
              // This lets us support copy and paste too
              if (key == 91 || (15 < key && key < 19) || (37 <= key && key <= 40)){
                  return;
              }
              $browser.defer(listener); // Have to do this or changes don't get picked up properly
          });

          $element.bind('paste cut', function() {
              $browser.defer(listener);
          });
      }

  };
}])

.filter('soc', function () {

    return function (number) {
      /* 
      @param {Number | String} number - Number that will be formatted as telephone number
      Returns formatted number: (###) ###-####
        if number.length < 4: ###
        else if number.length < 7: (###) ###

      Does not handle country codes that are not '1' (USA)
      */
        if (!number) { return ''; }

        number = String(number);

        // Will return formattedNumber. 
        // If phonenumber isn't longer than an area code, just show number
        var formattedNumber = number;

    // if the first character is '1', strip it out and add it back
    var c = '';

    // # (###) ###-#### as c (area) front-end
    var area = number.substring(0,3);
    var front = number.substring(3, 5);
    var end = number.substring(5, 9);

    if (front) {
      formattedNumber = ( area + "-" + front);	
    }
    if (end) {
      formattedNumber += ("-" + end);
    }
    return formattedNumber;
    };
})

.name;
export default sample;



