class LoginController {
  constructor($state, LoginSrv, ToastSrv, AuthService, $window, $scope,$http,constants, ClientSrv, blockUI, blockUIConfig) {
    'ngInject';

    this.$state = $state;
    this.$window = $window;
    this.$scope = $scope;
    this.constants=constants;
    this.LoginSrv = LoginSrv;
    this.ToastSrv = ToastSrv;
    this.AuthService = AuthService;
    this.tab = true;
    this.user = true;
    this.error = false;
    this.submitted = false;
    // this.client=false;
    this.$http=$http;
    this.ClientSrv = ClientSrv;
    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
  }


  submitData(formvalid) {
    if (formvalid) {
      if (this.tab1) {
        // this.client=true;
        this.clientLogin();
      }
      else {
        this.submitted = true;
        this.login();
      }
    }
  }

  //---------------Client login function: arun -----------------//
  clientLogin() {
    this.blockUI.start();
    var data = {
      username: this.client_username,
      password: this.password,
    }
    this.ClientSrv.login(data, (resp) => {
      if(resp.data.success) {
        this.blockUI.stop();
        this.$state.go('clientportal')
        this.ToastSrv.notify('Welcome ' + resp.data.name, 'success')  
      this.AuthService.setCredentials(resp.data.token);
      } else {
        this.blockUI.stop();
        this.ToastSrv.notify(resp.data.message, 'error')
          this.error = true;
      }
    })
  }
  //---------------Client login function ends: arun ------------//

  /**
   * Function to authenticate
   */
  login() {
    this.blockUI.start();
    var data = {
      email: this.username,
      password: this.password
    }
    if (this.user) {
      this.LoginSrv.login(data, (response) => {
        if (response.data.success == true) {
          this.blockUI.stop();
          this.$state.go('carleton.dashboard');
          if(response.data.name == null || response.data.name == '') {
            this.ToastSrv.notify('Welcome ' + response.data.email, 'success')
          } else {
            this.ToastSrv.notify('Welcome ' + response.data.name, 'success')
          }
          this.AuthService.setCredentials(response.data.token);
        }
        else {
          this.blockUI.stop();
          this.ToastSrv.notify(response.data.message, 'error')
          this.error = true;
        }
      })
    }
  } //login function ends

  forgotPassword() { this.$state.go('forgotPassword'); }
}
export default LoginController;



