import template from './addevent.html'; //modal for add event
import template1 from './editevent.html'; //modal for edit event
import template2 from './deleteevent.html'; //modal for delete event
import template3 from './todo.html'; //modal for delete event


class CalendarController {
  constructor(calendarConfig, $uibModal, $state, moment, $rootScope, RecordSrv, CalendarSrv, dashboardSrv, ToastSrv, $http, constants, blockUI, blockUIConfig,$filter) {
    "ngInject";
    this.name = 'calendar';
    this.moment = moment;
    this.$state = $state;
    this.$http = $http;
    this.$filter=$filter
    this.constants = constants;
    this.ToastSrv = ToastSrv;
    $rootScope.CalendarSrv = CalendarSrv
    $rootScope.ToastSrv = ToastSrv;
    this.CalendarSrv = CalendarSrv;
    this.dashboardSrv = dashboardSrv;
    $rootScope.dashboardSrv = dashboardSrv;
    this.RecordSrv = RecordSrv;
    this.calendarConfig = calendarConfig;
    this.$uibModal = $uibModal;
    this.$rootScope = $rootScope;

    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.blockUIConfig.resetOnException = true;
    this.loggedUser = JSON.parse(localStorage.getItem('user'));
    
    this.calendarView = 'month'; //These variables MUST be set as a minimum for the calendar to work
    this.viewDate = new Date();
    this.calEvent = [];

    this.initialServices();

    this.actions = [{ //for calander actions like delete and edit
        label: '<button  type="button" ng-click="$rootScope.editEventmodal(args.calendarEvent);"  title="Click to Edit" class=\'btn btn-xs btn-outline btn-info \'><i class=\'fa fa-pencil hand-over\' ng-click="$rootScope.editEventmodal(args.calendarEvent);"  title="Click to Edit"></i></button>',
        onClick: function (args) {
          $rootScope.editEventmodal(args.calendarEvent);
        }
      },
      {
        label: '<button type="button"  title="Click to Delete" class=\'btn btn-xs btn-outline btn-warning\'><i class=\'fa fa-trash\'></i></button>',
        onClick: function (args) {
          $rootScope.deleteEventmodal(args.calendarEvent);
        }
      }
    ];

    this.eventTimesChanged = function (args) {
      $rootScope.editEventmodal(args);

    };
    // rootscope used to access the funtion globally in CalendarController
    $rootScope.newEvent = this.newEvent.bind(this); //function to open modal
    $rootScope.addEvent = this.addEvent.bind(this); //function to add event
    $rootScope.editEvent = this.editEvent.bind(this); //function to add event
    $rootScope.deleteEvent = this.deleteEvent.bind(this); //function to add event
    $rootScope.editEventmodal = this.editEventmodal.bind(this); //function to edit event
    $rootScope.deleteEventmodal = this.deleteEventmodal.bind(this); //function to delete event

    $rootScope.changeTodo = this.changeTodo.bind(this); //bind function to Todo
    $rootScope.initialServices = this.initialServices.bind(this);

    //Getting all properties from DB for get propertyname
    this.CalendarSrv.getRecords((data) => {
      $rootScope.properties = data.data.results;
    })

    //Getting all groups from DB for get groupname
    this.CalendarSrv.getGroups((data) => {
      
      $rootScope.groups = data.data;
    })
  }

  initialServices() {
    this.blockUI.start();

    this.CalendarSrv.getEvents((data) => {
      this.events = data;
      this.calEvent = []
      this.events.forEach(element => {
        var cal = {
          id: element.id,
          title: element.name,
          color: this.calendarConfig.colorTypes.info,
          startsAt: element.log,
          endsAt: element.log,
          description: element.description,
          group : element.group ? element.group : '',
          group_name: element.group_details ? element.group_details.group_name : '',
          propertyname: element.property_details ? element.property_details.name : '',
          property: element.property_details ? element.property_details.id : '',
          draggable: true,
          resizable: true,
          actions: this.actions,

        }
        this.calEvent.push(cal)
      })
    }) //get all events and dispaly to calendar

    this.dashboardSrv.toDoSrv((resp) => { //get all todo and dispaly
      this.toDodata = resp;
      this.compList = [];
      this.toDodata.map(x => (x.is_complete === false) ? this.compList.push(x.id) : '')
      this.$rootScope.pendingTodo = (this.toDodata.map(data => {
        if (!data.is_complete) {
          return {
            data
          };
        }
      }).filter(Boolean));
      this.blockUI.stop();
    })
  }
  changeView(va) {
    this.calender = va;
  }

  newEvent(eventdat) {
    var scope=this
    this.$uibModal.open({
      template,
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'moment', function ($uibModalInstance, $rootScope, moment) {
        this.properties = $rootScope.properties;
        this.groups = $rootScope.groups;
        var grpName = '';

        this.eventdatetime = moment(eventdat).format('YYYY-MM-DD HH:mm');
        this.close = function () {
          $uibModalInstance.close();
        }
        this.eventadd = function () {
         
          this.myevent.log = moment(this.eventdatetime).toISOString();
        //  this.myevent.added_by = '1';
          this.myevent.added_by = scope.loggedUser.user;
          this.myevent.property=this.myevent.property ? this.myevent.property : '';
          this.myevent.group=this.myevent.group ? this.myevent.group : '';
          // if (this.gid) {
          //   grpName = 'group=' + this.gid;
          // } else {
          //   grpName = '';
          // }
          $rootScope.addEvent(this.myevent);
          $uibModalInstance.close();
        }
        this.open1 = function () { //open datetime picker inside modal
          this.popup1 = true;
        };
      }],
      controllerAs: 'vm'
    })
  }

  editEventmodal(eventedit) {
    this.$uibModal.open({
      template: template1,
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'moment', function ($uibModalInstance, $rootScope, moment) {
        this.myevent = {};
        this.properties = $rootScope.properties;
        this.groups = $rootScope.groups;
        this.eventedit = eventedit;
        this.eventedit.startsAt = moment(eventedit.startsAt).format('YYYY-MM-DD HH:mm');
        this.eventedit.name = eventedit.title;
        this.close = function () {
          $uibModalInstance.close();
        }
        this.editevent = function () {
          // this.myevent.log = moment(this.eventdatetime).toISOString();
          $rootScope.editEvent(this.eventedit);
          $uibModalInstance.close();
        }
        this.open1 = function () { //open datetime picker inside modal
          this.popup1 = true;
        };
      }],
      controllerAs: 'vm'
    })
  }

  deleteEventmodal(eventdelete) {
    this.$uibModal.open({
      template: template2,
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'moment', function ($uibModalInstance, $rootScope, moment) {
        this.eventdelete = eventdelete;
        this.close = function () {
          $uibModalInstance.close();
        }
        this.deleteEvent = function () {
          $rootScope.deleteEvent(this.eventdelete);
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vm'
    })
  }

  addEvent(event, grpName) {
    this.CalendarSrv.addEvent(event, (response) => {
      if (response.success == true) {
        this.ToastSrv.notify('Event Added Successfully', 'success')
        this.initialServices()
        this.$state.reload();

      } else {
        this.ToastSrv.notify('Event failed to add', 'error')
        this.error = true;
      }
    })
  }

  editEvent(event) {
    var data = {
      id: event.id,
      name: event.name,
      log: this.moment(event.startsAt).toISOString(),
      description: event.description,
      group : event.group ? event.group : '',
      property: event.property ? event.property : '',
    }
    this.CalendarSrv.editEvent(data, event.id, (response) => {
      if (response.success == true) {
        this.ToastSrv.notify('Event Updated Successfully', 'success')
        this.initialServices()
        this.$state.reload();

      } else {
        this.ToastSrv.notify('Event failed to edit', 'error')
        this.error = true;
      }
    })
  }

  deleteEvent(event) {
    this.CalendarSrv.deleteEvent(event.id, (response) => {
      if (response.success == true) {

        this.ToastSrv.notify('Event deleted Successfully', 'success')
        this.initialServices()
        this.$state.reload();

      } else {
        this.ToastSrv.notify('Event failed to delete', 'error')
        this.error = true;
      }
    })
  }
  weekSelection(){
   
    this.cellIsOpen = false
    for (var i = 0; i < this.calEvent.length; i++) {  
    this.calEvent[i].title = this.$filter('limitTo')(this.events[i].name, 11)+"...."
}
  }
  timespanClicked(date, cell) {
  
    for (var i = 0; i < this.calEvent.length; i++) {
      
        this.calEvent[i].title = this.events[i].name;
    
    }
    if (this.calendarView === 'month') {
      if ((this.cellIsOpen && this.moment(date).startOf('day').isSame(this.moment(this.viewDate).startOf('day'))) ||
        cell.events.length === 0 || !cell.inMonth) {
        this.cellIsOpen = false
        if (cell.events.length === 0) {
          this.newEvent(this.moment(date).startOf('day'));
        }
      } else {
        this.cellIsOpen = true
        this.viewDate = date

      }
    } else if (this.calendarView === 'year') {
      if ((this.cellIsOpen && this.moment(date).startOf('month').isSame(this.moment(this.viewDate).startOf('month'))) ||
        cell.events.length === 0) {
        this.cellIsOpen = false
      } else {
        this.cellIsOpen = true
        this.viewDate = date
      }
    }
  }

  getSearchEvent(val) {
    return this.CalendarSrv.getSearchEventSrv(val).then((result) => {
      this.searchResult = result;
     


      return result.map(function (item) {
        // return "Event Name : " +item.name + " ,  Record No : " + item.property;
        return item;

      });
    })
  }
  onSearchEvent(searchevent) {
    this.eventSelected = "";
    this.$uibModal.open({
      template: template1,
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'moment', function ($uibModalInstance, $rootScope, moment) {
        this.myevent = {};
        this.properties = $rootScope.properties;
        this.eventedit = searchevent;
        this.eventedit.startsAt = moment(searchevent.log).format('YYYY-MM-DD HH:mm');
        this.eventedit.name = searchevent.name;
        this.close = function () {
          $uibModalInstance.close();
        }
        this.editevent = function () {
          // this.myevent.log = moment(this.eventdatetime).toISOString();
          $rootScope.editEvent(this.eventedit);
          $uibModalInstance.close();
        }
        this.open1 = function () { //open datetime picker inside modal
          this.popup1 = true;
        };
      }],
      controllerAs: 'vm'
    })
  }

  cancelSearch() { //on cancel search imports
    this.eventSelected = '';
  }

  // ******toDO funtion******//
  toDO() {
    this.blockUI.start();
    this.dashboardSrv.toDoSrv((resp) => {
      this.blockUI.stop();
      this.toDodata = resp;
      this.$rootScope.pendingTodo = (this.toDodata.map(data => {
        if (!data.is_complete) {
          return {
            data
          };
        }
      }).filter(Boolean));
      this.toDOModal(this.toDodata)
    })
  }

  changeTodo(id, bool) {
   
    var data = {
      is_complete: bool
    }
    this.dashboardSrv.updatetoDoSrv(id, data, (resp) => {
      this.dashboardSrv.toDoSrv((resp) => { //get all todo and dispaly
        this.toDodata = resp;
        this.compList = [];
        this.toDodata.map(x => (x.is_complete === false) ? this.compList.push(x.id) : '')
        this.$rootScope.pendingTodo = (this.toDodata.map(data => {
          if (!data.is_complete) {
            return {
              data
            };
          }
        }).filter(Boolean));
        this.ToastSrv.notify('Task Updated Successfully', 'success')
      })
      // this.$rootScope.updateData(this.toDodata);
      // this.initialServices()

    })
  }

  toDOModal(data) {
    this.$uibModal.open({
      template: template3,
      backdrop: 'static',
      size: 'lg',
      controller: ['$uibModalInstance', '$rootScope', 'moment', function ($uibModalInstance, $rootScope, moment) {
        this.todoList = true;
        this.addToDo = false;
        this.toDodata = data;
        this.compList = [];
        this.toDodata.map(x => (x.is_complete === false) ? this.compList.push(x.id) : '')
        var date = new Date();
        var firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
        var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);
        this.dateOptions = {
          maxDate: lastDay,
          minDate: firstDay,
        }
        this.openToDo = function () { //open datetime picker inside modal
          this.popupTodo = true;
        };
        this.changeTodoModal = (id, bool) => {
          $rootScope.changeTodo(id, bool);
        }

        this.add = function() {
          this.task['ignore_calendar'] = true;
          this.task['log'] = moment(this.eventdatetimeTodo).toISOString();
          $rootScope.CalendarSrv.addEvent(this.task, (response) => {
            if (response.success == true) {
              this.todoList = true;
              this.addToDo = false;
              this.compList = [];
              $rootScope.ToastSrv.notify('Task added Successfully', 'success')
              $rootScope.dashboardSrv.toDoSrv((resp) => {
                this.toDodata = resp;
                $rootScope.pendingTodo = (this.toDodata.map(data => {
                  if (!data.is_complete) {
                    return {
                      data
                    };
                  }
                }).filter(Boolean));
                this.toDodata.map(x => (x.is_complete === false) ? this.compList.push(x.id) : '')
              })
            } else {
              $rootScope.ToastSrv.notify('Task failed to add', 'error')
              this.error = true;
            }
          })
        }

        this.deleteEvent = function(id, index) {
          $rootScope.CalendarSrv.deleteEvent(id, (response) => {
            if (response.success == true) {
              this.toDodata.splice(index, 1);
              this.compList = [];
              $rootScope.ToastSrv.notify('Event deleted Successfully', 'success')
              this.toDodata.map(x => (x.is_complete === false) ? this.compList.push(x.id) : '')
              $rootScope.pendingTodo = (this.toDodata.map(data => {
                if (!data.is_complete) {
                  return {
                    data
                  };
                }
              }).filter(Boolean));
            } else {
              this.ToastSrv.notify('Event failed to delete', 'error')
              this.error = true;
            }
          })
        }

        $rootScope.updateData = () => {
          this.toDodata = data;
          this.compList = [];
          this.toDodata.map(x => (x.is_complete === false) ? this.compList.push(x.id) : '')
        }

        this.close = () => {
          $uibModalInstance.close();
        }


      }],
      controllerAs: '$ctrl'
    })
  }
  // ******toDO funtion******

  addTask() {

  }

}

export default CalendarController;



