class ResetPasswordController {
  constructor($state, LoginSrv, $uibModal, $rootScope) {
    'ngInject';
    this.$state = $state;
    this.$uibModal = $uibModal;
    this.LoginSrv = LoginSrv;
    this.$rootScope = $rootScope;
    this.passCombiErr = false;
    this.currPassErr = false;
  }
  resetPassword() {
    if (this.newPassword == this.newPasswordConf) {
      var data = {
        current_password: this.currPassword,
        new_password: this.newPasswordConf
      }
      this.LoginSrv.resetPassword(data, (response) => {
        var message = response.data.message;
// modal function for reset password success start;
        if (response.data.success == true) {
          this.$uibModal.open({
            size : 'sm',
            templateUrl: 'resetPassword.html',
            backdrop:'static',
            controller: ['$uibModalInstance', '$rootScope', '$state', function ($uibModalInstance, $rootScope, $state) {
              this.message = message;
              this.close = function () {
                $rootScope.$state.go('login')
                $uibModalInstance.close();


              }
            }],
            controllerAs: 'vm'

          });
        } else {
          this.currPassErr = true;
          this.message = message;
        };
      })
    }
    else {
      this.passCombiErr = true;

    }
  }
}

export default ResetPasswordController;



