  openSetStatusModal() {
    this.$uibModal.open({
      templateUrl: 'setStatusModal.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        $rootScope.statusList();
        this.byStatus = [];
        this.setstatus = function () {
          if ($rootScope.propertIds.length == 0) {
            $rootScope.ToastSrv.notify('Please select Records', 'error')
            $uibModalInstance.close();
          } else {
            this.statusData = {
              properties: $rootScope.propertIds,
              status: this.byStatus,
              send_email: false,
              contacts: '',
              assigned_to: '',
              delete: '',
              send_letter: '',
              message: '',
              subject: '',
              to: '',
              bcc: '',
              salutation: '',
              font: '',
              send_text: ''

            }
         
            $rootScope.RecordSrv.bulkRecord(this.statusData, (resp) => {
              $rootScope.specificDateSelect();
              $uibModalInstance.close();
            })
          }
        }

        this.addNewStatus = function() {
          this.dataArray = [];
          this.data = {
            status : this.status,
            deletable : true
          }
          this.dataArray.push(this.data);
          $rootScope.RecordSrv.addStatus(this.dataArray, (res) => {
            if(!res.data.success) {
              $rootScope.ToastSrv.notify('Status Already Exists', 'error');
            }else {
              $rootScope.RecordSrv.getStatus((resp) => {
                $rootScope.statusListFilter = resp.data;
                this.showAddStatus = false;
                this.status = '';
                this.byStatus.push(res.data.data[0].id);
                $rootScope.ToastSrv.notify('Status added successfully', 'success');
              })
            }
          })
        }

        this.removestatusModal = function(name, id, assignedOrNot) {
          $rootScope.showMessage("Deleting..");
          if(assignedOrNot == false) {
            $rootScope.RecordSrv.removeSatus(id, (resp) => {
              $rootScope.RecordSrv.getStatus((resp) => {
                $rootScope.statusListFilter = resp.data;
                $rootScope.ToastSrv.notify('Status Deleted Successfully', 'success');
              })
            })
          } else {
            $rootScope.ToastSrv.notify(name +' is assigned to another record, thus cannot be deleted !!!!', 'error');
          }
          $rootScope.closeMsg();
        }

        this.cancel = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vm'
    })
  }

