  goToMap(data) {
   
    var address = '';
    if (data != null) {
      address += data.address_line1 ? data.address_line1 : '';
      address += data.address_line2 ? ', ' + data.address_line2 : '';
      address += data.address_line2 ? ', ' + data.address_line2 : '';
      window.localStorage.newTab = JSON.stringify({
        state: "map",
        params: {
          name: address
        }
      });
      window.open(document.location.origin);
    }
  }

