class ContactsController {
  constructor($uibModal, $rootScope, RecordSrv, constants, ContactSrv, ToastSrv, blockUI, blockUIConfig) {
    'ngInject';
    this.name = 'contacts';

    this.$uibModal = $uibModal;
    this.ContactSrv = ContactSrv;
    this.ToastSrv = ToastSrv;

    $rootScope.ContactSrv = ContactSrv;
    $rootScope.ToastSrv = ToastSrv;
    $rootScope.RecordSrv = RecordSrv;
    $rootScope.constants = constants;

    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.blockUIConfig.resetOnException = true;
    this.myBlockUI = this.blockUI.instances.get('myBlockUI');

    $rootScope.companyList = this.companyList.bind(this);
    $rootScope.professionList = this.professionList.bind(this);
    $rootScope.getContacts = this.getContacts.bind(this); // For accessing inside modals
    $rootScope.deleteSelectedContacts = this.deleteSelectedContacts.bind(this); // For accessing inside modals
    this.loggedUser = JSON.parse(localStorage.getItem('user'));

    this.companyList = this.companyList.bind(this);

    this.addContacts = false;
    this.editContact = false;
    this.showDetails = false;
    this.listView = true;
    this.contactDetails = {};
    this.updatedDetails = {};

    this.maxSize = 3; //pagination  intial values
    this.pageItems = 12;
    this.CurrentPage = 1;


    this.professionList();
    this.companyList();
    this.getContacts();
    this.init();

  }

  init() {
    // ******DYNAMIC INPUT FIELD INTIALISATION******//

    this.contactPhone = [{
      "id": 1,
      "type": "Mobile",
      "phone": "",
      "extension" : ""
    }];
    this.index = this.contactPhone.length;

    this.contactMail = [{
      "id": 1,
      "type": "email",
      "email": ""
    }];
    this.indexMail = this.contactMail.length;
  }

  // ******DYNAMIC PHONE INPUT FIELD******//
  addNewChoice() {
    var newItemNo = ++this.index;
    this.contactPhone.push({
      'id': newItemNo,
      "type": "Mobile",
      "phone": "",
      "extension" : ""
    });
  };



  removeChoice(id) {
    if (this.contactPhone.length <= 1) {
      return;
    }
    var index = -1;
    var comArr = eval(this.contactPhone);
    for (var i = 0; i < comArr.length; i++) {
      if (comArr[i].id === id) {
        index = i;
        break;
      }
    }
    if (index === -1) {}
    this.contactPhone.splice(index, 1);
  };

  // ******DYNAMIC MAIL INPUT FIELD******//
  addNewChoiceMail() {
    var newItemNo = ++this.indexMail;
    this.contactMail.push({
      'id': newItemNo,
      "type": "email",
      "email": ""
    });
  };

  removeChoiceMail(id) {
    if (this.contactMail.length <= 1) {
      return;
    }
    var index = -1;
    var comArr = eval(this.contactMail);
    for (var i = 0; i < comArr.length; i++) {
      if (comArr[i].id === id) {
        index = i;
        break;
      }
    }
    if (index === -1) {}
    this.contactMail.splice(index, 1);
  };



  getContacts() {
    this.init();
    this.myBlockUI.start();
    this.contactSearchSelected = '';
    this.ContactSrv.getContacts(this.CurrentPage, (resp) => {

      this.data = resp.data.results;
      this.totalItems = resp.data.count;
      this.myBlockUI.stop();
    })
  }
  professionList() {
    this.ContactSrv.getProfession((resp) => {
      this.dropdownData = resp.data;
    })
  }

  removeProfession(id) {
    this.ContactSrv.removeProfessionSrv(id, (resp) => {
      if (resp.status < 300 || resp.success == true) {
        this.professionList();
        this.ToastSrv.notify('Profession Deleted sucessfully', 'success');
      } else {
        this.ToastSrv.notify('Profession Deletion Failed', 'error');
        this.professionList();

      }
    })
  }

  companyList() {
    this.ContactSrv.getCompany((resp) => {
      this.companyOptions = resp.data;

    })
  }

  removeCompany(id) {
    this.ContactSrv.removeCompanySrv(id, (resp) => {

      if (resp.status < 300 || resp.success == true) {
        this.ToastSrv.notify('Company Deleted sucessfully', 'success');
        this.companyList();
      } else {
        this.ToastSrv.notify('Company Deletion Failed', 'error');
      }
    })
  }

  pageChanged() {
    this.CurrentPage = this.CurrentPage;
    this.getContacts();
  };

  checkAll() {
    if (this.allSelect) {
      this.checkList = this.data.map(function (item) {
        return item;
      });
      this.data.forEach(element => {
        element.selected = true;
      });
    } else {
      this.checkList = [];
      this.data.forEach(element => {
        element.selected = false;
      });
    }
  }

  select(item) {
    item.selected ? item.selected = false : item.selected = true;
  }

  addContact() {
    this.addContacts = true;
  }

  cancelForm() {
    this.addContacts = false;
    this.editContact = false;
    this.contactDetails = {};
    this.cont = {};
    this.showDetails = false;
    this.getContacts();
    this.contactSearchSelected = '';
  }

  editContacts(contact) {
    this.editContact = true;
    this.cont = contact;
    this.showDetails = false;
    this.contactMail = contact.email_details.length != 0 ? contact.email_details : this.contactMail;
    this.contactPhone = contact.phone_details.length != 0 ? contact.phone_details : this.contactPhone;


  }

  savecontacts() {
    if (this.contactDetails.length != 0) {
      var data = {
        company: this.contactDetails.company ? this.contactDetails.company : '',
        name: this.contactDetails.name,
        profession: this.contactDetails.profession ? this.contactDetails.profession : '',
        contact_address: this.contactDetails.address,
        email_details: (this.contactMail = this.contactMail.map(email => {
          if (email.email != '') {
            return {
              email: email.email
            };
          }
        }).filter(Boolean)),
        phone_details: this.contactPhone = (this.contactPhone.map(phoneNo => {
          if (phoneNo.phone != '') {
            return {
              phone: phoneNo.phone,
              extension : phoneNo.extension
            };
          }
        }).filter(Boolean)),


        note_details: [],
        is_contact: true,
      }
      var list = [];
      list.push(data);
      this.showDetails = false;
      this.blockUI.start();


      this.ContactSrv.addContactSrv(list, (resp) => {
        if (resp.status < 300 || resp.success) {
          this.ToastSrv.notify('Contact has been added successfully.', 'success')
          this.blockUI.stop();
          this.getContacts();
          this.cancelForm();
        } else {
          this.blockUI.stop();
          this.ToastSrv.notify('Contact failed to Add.!', 'error')
        }

      })
    }
  }

  updatecontacts(id) {
    // this.blockUI.start();
    this.updatedDetails = {
      company: this.cont.company,
      name: this.cont.name,
      profession: this.cont.profession,
      contact_address: this.cont.contact_address,
      email_details: (this.contactMail = this.contactMail.map(email => {
        if (email.email != '') {
          return {
            email: email.email
          };
        }
      }).filter(Boolean)),
      phone_details: this.contactPhone = (this.contactPhone.map(phoneNo => {
        if (phoneNo.phone != '') {
          return {
            phone: phoneNo.phone,
            extension : phoneNo.extension
          };
        }
      }).filter(Boolean)),
    }
    this.ContactSrv.updateContactsSrv(this.updatedDetails, id, (resp) => {
      if (resp.status < 300 || resp.success) {
        this.blockUI.stop();
        this.ToastSrv.notify('Contact has been updated successfully.!', 'success')
        this.getContacts();
        this.cancelForm();
      } else {
        this.blockUI.stop();
        this.ToastSrv.notify('Contact failed to Add.!', 'error')
      }
    })

  }



  deleteSelectedContacts() {
    this.deleteList = [];
    this.checkList.forEach(element => {
      this.deleteList.push(element.id);
    });
    this.ContactSrv.deleteContactSrv(this.deleteList, (resp) => {
      if (resp.status < 300 || resp.success == true) {
        this.getContacts();
        this.checkList = [];
        this.deleteList = [];
        this.allSelect = false;
        this.ToastSrv.notify('Contacts Deleted sucessfully', 'success')
      } else {
        this.ToastSrv.notify('Contacts Deletion Failed', 'error')
      }

    })
  }

  getSearchContact(val) { //search in Contacts
    return this.ContactSrv.getSearchContactSrv(val).then((result) => {
      this.searchResult = result;
      return result.map(function (item) {
        return item;

      });
    })
  }

  onSearchContact(searchResult) { //on select searchContacts
    this.data = [searchResult];
  }

  cancelSearch() { //on cancel search Contacts
    this.contactSearchSelected = '';
    this.getContacts();
    this.loading = false;
  }

  // ********************/MODALS/****************//

  //MODAL to show delete contact warning
  deleteUserModal(contact) {
    this.$uibModal.open({
      templateUrl: 'deleteuser.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        this.contact = contact; //only accessable in modal
        this.delete = function () {
          $rootScope.ContactSrv.deleteContacts(this.contact, (resp) => {
            if (resp.status < 300 || resp.success) {
              $rootScope.getContacts();
              $rootScope.ToastSrv.notify('Contact has been deleted successfully.!', 'success')
              $uibModalInstance.close();
            }
          })
        }

        this.close = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vx'
    })
  } //Delete warning modal ends


  deleteSelectedModal() {
    this.$uibModal.open({
      templateUrl: 'deleteselected.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        this.deleteSelected = function () {
          $rootScope.deleteSelectedContacts();
          setTimeout(function () {
            $uibModalInstance.close();
          }, 1000);

        }
        this.close = function () {
          $uibModalInstance.close();
        }
      }],

      controllerAs: 'vz'
    })
  } //Delete warning modal ends


  openCompanyModal() {
    this.$uibModal.open({
      templateUrl: 'companyModal.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'blockUI', function ($uibModalInstance, $rootScope, blockUI) {
        this.data = {
          'deletable': true
        };
        this.blockUI = blockUI;
        this.addProfession = function () {
          this.blockUI.start();
          $rootScope.ContactSrv.addCompanySrv(this.data, (resp) => {
            this.blockUI.stop();
            if (resp.status < 300 || resp.success) {
              $rootScope.ToastSrv.notify('Company has been added successfully.', 'success')
              $rootScope.companyList();
              $uibModalInstance.close();

            }

          })
        }
        this.close = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vn'
    })
  }

  openProfessionModal() {
    this.$uibModal.open({
      templateUrl: 'professionModal.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', 'blockUI', function ($uibModalInstance, $rootScope, blockUI) {
        this.data = {
          'deletable': true
        };
        this.blockUI = blockUI;
        this.addProfession = function () {
          this.blockUI.start();
          $rootScope.ContactSrv.addProfession(this.data, (resp) => {
            this.blockUI.stop();
            if (resp.status < 300 || resp.success) {
              $rootScope.ToastSrv.notify('Profession has been added successfully.', 'success')
              $rootScope.professionList();
            }
          })
          $uibModalInstance.close();
        }
        this.close = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vm'
    })
  }

  openViewContact(contact) {
    this.$uibModal.open({
      templateUrl: 'viewContactModal.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        this.data = contact; //only accessable in modal
        this.close = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vw'
    })
  }

  openAddNoteModal(contact) {
    this.$uibModal.open({
      templateUrl: 'addNoteModal.html',
      backdrop: 'static',
      size: 'lg',
      controller: ['$uibModalInstance', '$rootScope', 'LoginSrv', function ($uibModalInstance, $rootScope, LoginSrv) {

        this.saveClicked = false;
        this.LoginSrv = LoginSrv;
        this.LoginSrv.getUser((data) => { //get login user details//
          this.userData = data;

        })
        var id = contact.id; //only accessable in modal
        if (contact.note_details.length > 0) {
          this.choices = contact.note_details;
          // if(!contact.note_details[0].added_by){contact.note_details[0].added_by=this.userData.user}

        } else {
          this.choices = [{}]
        }

        this.noteChanged = function (index) {
          this.choices[index].added_by = this.userData.user;
        };

        this.addNewChoice = function () {
          this.choices.push({
            added_by: this.userData.user
          });
        };

        this.removeNewChoice = function (index) {
          var newItemNo = this.choices.length - 1;
          if (newItemNo !== 0) {
            this.choices.splice(index, 1);
          } else if (newItemNo == 0) {
            this.choices[0] = {};
            this.btndisbled = true;
          }
        };

        this.showAddChoice = function (choice) {
          return choice === this.choices[this.choices.length - 1];
        };

        this.addNote = function () {
          Object.keys(this.choices).forEach((key) => (this.choices[key] == null) && delete this.choices[key]);
          var data = {
            note_details: this.choices
          }
          this.saveClicked = true;
          $rootScope.ContactSrv.addNoteSrv(data, id, (resp) => {
            if (resp.success || resp.status < 300) {
              this.saveClicked = false;
              $uibModalInstance.close();
              $rootScope.getContacts();
              $rootScope.ToastSrv.notify('Note has been updated successfully.', 'success')
            }
          })
        }
        this.close = function () {
          $rootScope.getContacts();
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vy'
    })
  }


  openSendMailModal(contact, email) {

    this.$uibModal.open({
      templateUrl: 'mailModal.html',
      backdrop: 'static',
      size: 'lg',
      controller: ['$uibModalInstance', '$rootScope', 'blockUI', 'blockUIConfig', function ($uibModalInstance, $rootScope, blockUI, blockUIConfig) {
        this.addNewTemplateModal = false;
        this.mailTemplateModal = true;
        this.editTemplateModal = false;
        this.blockUI = blockUI;
        this.contact = contact;
        this.contact.email = email;
        this.blockUIConfig = blockUIConfig;
        this.blockUIConfig.autoBlock = false;

        this.choices = [];


        this.addNewChoice = function () {
          this.choices.push({
            file: this.file
          });
          this.file = '';
          this.attList = this.choices.map(function (item) {
            return item.file;
          });
        };

        this.removeNewChoice = function (index) {
          this.choices.splice(index, 1);
        };
        this.clearField = function () {
          this.editTemplatesubject = '';
          this.editTemplateName = '';
          this.editTemplatemessage = '';
          this.mailSubject = '';
          this.mailMessage = '';
        }

        $rootScope.ContactSrv.getTemplateSrv((resp) => {
          this.templateList = resp;
        })

        $rootScope.ContactSrv.getSalutation((resp) => {
          this.salutations = resp;
        })

        this.cancelForm = () => {
          this.intialService();
          this.goToImports();
        }

        this.clearFile = () => { //clear file name && file
          this.file = '';
          this.filename = '';
          this.importData.file = '';
        }

        this.clearAll = () => { //clear all data
          this.clearFile();
          this.importData = [];
        }

        this.fileName = () => { //naming function onselect the file

          this.filename = this.file.name;
          this.file = this.file;
        }




        this.createNewTemplate = function () {
          var addNewTemplate = {
            level: this.addTemplateName,
            subject: this.addTemplatesubject,
            message: this.addTemplatemessage,

          }
          $rootScope.ContactSrv.createTemplateSrv(addNewTemplate, (resp) => {
            if (resp.status < 300 || resp.success) {
              $rootScope.ToastSrv.notify('Template Created successfully', 'success');

              $rootScope.ContactSrv.getTemplateSrv((resp) => {
                this.templateList = resp;
              })
              this.mailTemplateModal = true;
              this.addNewTemplateModal = false;

            }
          })
        }

        this.addNewSal = function() {
          this.salData = {
            title : this.salu,
            deletable : true
          }
          $rootScope.ContactSrv.addSalutation(this.salData, (resp) => {
            if(resp.success) {
              $rootScope.ContactSrv.getSalutation((res) => {
                this.salutations = res;
                this.showAddSalu = false;
                this.salu = '';
                this.salutation = resp.data.title;
                $rootScope.ToastSrv.notify('Salutation Added successfully', 'success');
              })
            } else {
              $rootScope.ToastSrv.notify('Salutation Already Exists', 'error');
            }
          })
        }

        this.deleteSalu = function(id) {
          $rootScope.ContactSrv.deleteSalutation(id,(resp) => {
            $rootScope.ContactSrv.getSalutation((res) => {
              this.salutations = res;
              this.salutation = '';
              $rootScope.ToastSrv.notify('Salutation Deleted successfully', 'success');
            })
          })
        }

        this.editMessageDisplay = function () {
          this.id = this.level.id;
          this.editTemplateName = this.level.level;
          this.editTemplatesubject = this.level.subject;
          this.editTemplatemessage = this.level.message;
        }

        this.editTemplate = function () {
          var editTemplate = {
            id: this.id,
            level: this.editTemplateName,
            subject: this.editTemplatesubject,
            message: this.editTemplatemessage,

          }
          $rootScope.ContactSrv.editTemplateSrv(editTemplate, (resp) => {
            if (resp.status < 300 || resp.success) {
              $rootScope.ToastSrv.notify('Template Updated successfully.', 'info');
              $rootScope.ContactSrv.getTemplateSrv((resp) => {
                this.templateList = resp;
              })
              this.mailTemplateModal = true;
              this.editTemplateModal = false;

            }
          })
        }

        this.sendMailDisplay = function () {
          this.mailSubject = this.level.subject;
          this.mailMessage = this.level.message;
        }

        this.sendMail = function () {
          if(this.mailSubject == '' || this.mailSubject == null) {
            this.mailSubject = ''
          }
          var sendMailData = {
            // status: [],
            // properties: '',
            // assigned_to: '',
            // delete: '',
            // send_email: true,
            // send_letter: '',
            // bcc: '',
            // send_text: '',
            // font: '',
            contacts: this.contact.id,
            salutation: this.salutation,
            subject: this.mailSubject,
            message: this.mailMessage,
            to: email,
            file: this.attList
          }
          // var fileList= this.attList;
          this.blockUI.start();
          $rootScope.ContactSrv.sendMailSrv(sendMailData, (resp) => {
            if (resp.status < 300 || resp.success) {
              this.blockUI.stop();
              $rootScope.ToastSrv.notify('Message Send successfully.', 'success');
              $uibModalInstance.close();
            }
          })
        }

        this.preview = function() {
          if(this.mailSubject == '' || this.mailSubject == null) {
            this.mailSubject = ''
          }
          var sendPrevData = {
            contacts: this.contact.id,
            salutation: this.salutation,
            subject: this.mailSubject,
            message: this.mailMessage,
            to: email,
          }
          $rootScope.RecordSrv.showMailPreview(sendPrevData, (resp) => {
            if (resp.data.success) {
              var url = $rootScope.constants.EXPORT_URL + resp.data.msg;
              window.open(url, '_blank');
            }
          })
        }


        this.close = function () {
          $uibModalInstance.close();
          this.clearField();
        }

      }],
      controllerAs: 'vt'
    })
  }


}

export default ContactsController;



