  //----------------function for opening Attchment view modal--------------------//
  openAttachmentModal() {
    this.$uibModal.open({
      templateUrl: 'attachmentsModal.html',
      size: 'lg',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope', '$scope', function ($uibModalInstance, $rootScope){
        $rootScope.file = [];
        this.indexValue =[];
        this.deleteFiles = function() {
          this.filesToDelete = [];
          for(var i=0;i<$rootScope.fileUploaded.length;i++) {
            if($rootScope.fileUploaded[i].isSelect) {
              if($rootScope.fileUploaded[i].id) {
                this.filesToDelete.push($rootScope.fileUploaded[i].id);
                this.indexValue.push($rootScope.fileUploaded.indexOf($rootScope.fileUploaded[i]));
              } else {
                this.indexValue.push($rootScope.fileUploaded.indexOf($rootScope.fileUploaded[i]));
              }
            }
          }
          if(this.filesToDelete.length != 0){
            $rootScope.RecordSrv.deleteAttachments(this.filesToDelete,(resp) => {
              for (var i = this.indexValue.length -1; i >= 0; i--) {
                $rootScope.fileUploaded.splice(this.indexValue[i],1);
              }
            })
          } else {
            for (var i = this.indexValue.length -1; i >= 0; i--) {
              $rootScope.fileUploaded.splice(this.indexValue[i],1);
            }
          }
          if($rootScope.fileUploaded.length == 0) {
            $rootScope.showViewattachment = false;
            $uibModalInstance.close();
          }
        }
        this.cancel = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'va'
    })
  }
  //--------------------view attachment modal ends--------------------------------//

