  //----------------function for opening delete record modal--------------------//
  openDeleteConfirmModal(id) {
    this.$uibModal.open({
      templateUrl: 'deleteConfirm.html',
      backdrop: 'static',
      controller: ['$uibModalInstance', '$rootScope','moment' , function ($uibModalInstance, $rootScope) {
        this.id = id;
        this.deleteSingleRecord = function() {
          for(var i=0;i<$rootScope.recordList.length;i++) {
            if($rootScope.recordList[i].id == this.id) {
              var curRecordInd = $rootScope.recordList.indexOf($rootScope.recordList[i]);
            }
          }
          this.nextRecordAfterDelete = $rootScope.recordList[curRecordInd+1];
          $rootScope.RecordSrv.deleteRecord(this.id, (resp) => {
            $rootScope.ToastSrv.notify('Record Deleted Successfully', 'success')
            $uibModalInstance.close();
            if(this.nextRecordAfterDelete == undefined) {
              if($rootScope.$stateParams.deleteFromGrp) {
                $rootScope.$state.go('carleton.groups');
              } else {
                $rootScope.$state.go('carleton.listRecords');
              }
            } else {
              $rootScope.$state.go('carleton.records', { 'id': this.nextRecordAfterDelete.id});
            }
          })
        }

        this.close = function() {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vsd'
    })
  }
