  calculateTotal(item, index, value) {
    if(value == 'debit') {
      if(item != '') {
        this.costs[index].creditDisable = true;
        this.costs[index].debitDisable = false;
      } else {
        this.costs[index].creditDisable = false;
      }
      this.$rootScope.recordDetails.debit_total = 0;
      for(var i=0;i<this.costs.length;i++) {
        if(this.costs[i].debit) {
          if(this.costs[i].debit == '') {
            this.costs[i].debit = 0;
            this.$rootScope.recordDetails.debit_total += parseFloat(this.costs[i].debit);
          } else {
            this.$rootScope.recordDetails.debit_total += parseFloat(this.costs[i].debit);
          }
        }
      }
    } else if(value == 'credit'){
      if(item != '') {
        this.costs[index].creditDisable = false;
        this.costs[index].debitDisable = true;
      } else {
        this.costs[index].debitDisable = false;
      }
      this.$rootScope.recordDetails.credit_total = 0;
      for(var i=0;i<this.costs.length;i++) {
        if(this.costs[i].credit) {
          if(this.costs[i].credit == '') {
            this.costs[i].credit = 0;
            this.$rootScope.recordDetails.credit_total += parseFloat(this.costs[i].credit);
          } else {
            this.$rootScope.recordDetails.credit_total += parseFloat(this.costs[i].credit);
          }
        }
      }
    }
  }

  securityLengthError(value) {
    if(value.length < 11 && value.length != 0) {
      this.showSecurityError = true;
    } else {
      this.showSecurityError = false;
    }
  }

  addProspectsRow() {
    this.nCount = 0;
    this.profCount   = 0;
    this.nameCount = 0;
    for(var i=0; i<this.prospects.length; i++) {
      this.prospects[i].removeBtn = true;
    }
    this.prospects.push({type:"", name:"", person:"", phone:"", email:"", fax:"", removeBtn:true, uniqueName: this.getRandomName()});
    var index = this.prospects.length-1;
  }

  removeProspectsRow($index) {
    this.prospects.splice($index, 1);
    this.multipleName = [];
    for(var i=0;i<this.propspectTypeList.length;i++) {
      if(this.propspectTypeList[i].profession == 'Prospect') {
        var prospectId = this.propspectTypeList[i].id;
      }
    }
    for(var removed of this.prospects) {
      if(removed.type == prospectId) {
        if(removed.person.id ){
          if(removed.person.name) {
            this.multipleName.push(removed.person.name);
          } else {
            delete removed.person;
          } 
        } else {
          this.multipleName.push(removed.person);
        }
      }
    }
  }

  afterRemoving(item) {
    for(var i=0;i<this.propspectTypeList.length;i++) {
      if(this.propspectTypeList[i].profession == 'Prospect') {
        var prospectId = this.propspectTypeList[i].id;
      }
    }
    for(var i=0;i<this.prospects.length;i++) {
      if(this.prospects[i].type == prospectId){
        if(this.prospects[i].person && this.prospects[i].person.id) {
          if(this.prospects[i].person.name == item) {
            this.prospects[i].type = '';
            this.prospects[i].disabled = false;
            this.prospects[i].nameDisable = true;
            this.prospects[i].disableProspect = false;
            this.prospects[i].typeDisable = false;
            this.prospects[i].compDisable = false;
          } 
        } else{
          if(this.prospects[i].person == item) {
            this.prospects[i].type = '';
            this.prospects[i].disabled = false;
            this.prospects[i].nameDisable = true;
            this.prospects[i].disableProspect = false;
            this.prospects[i].typeDisable = false;
            this.prospects[i].compDisable = false;
          }
        }
      }
    }
  }

  afterSelection(item) {
    for(var i=0;i<this.propspectTypeList.length;i++) {
      if(this.propspectTypeList[i].profession == 'Prospect') {
        var prospectId = this.propspectTypeList[i].id;
      }
    }
    this.prospectsToPush = {
      type:prospectId, 
      name:"", 
      person:item, 
      phone:"", 
      email:"", 
      fax:"", 
      is_contact : false,
      removeBtn:true,
      disabled : true,
      disableProspect : true,
      uniqueName: this.getRandomName()
    }
    if(this.prospects.length != 0) {
      for(var i=0;i<this.prospects.length;i++) {
        if(this.prospects[i].type == '' && this.prospects[i].name == '' && this.prospects[i].person == '') {
          var ind = this.prospects.indexOf(this.prospects[i]);
          this.prospects.splice(ind, 1)
        }
      }
    }
    this.prospects.push(this.prospectsToPush);
  }

  fillPersonName(type, person) {
    for(var i=0;i<this.propspectTypeList.length;i++) {
      if(this.propspectTypeList[i].profession == 'Prospect') {
        var prospectId = this.propspectTypeList[i].id;
      }
    }
    if(type == prospectId) {
      if(person.id) {
        if(person.name) {
          this.multipleName.push(person.name);
        } else {
          delete person.name;
        } 
      } else {
        this.multipleName.push(person);
      }
    }
  }

  addCostRow() {
    for(var i=0; i<this.costs.length; i++) {
      this.costs[i].removeCostBtn = true;
    }
    this.costs.push({description:"", debit: "", credit: "", debitDisable : false, creditDisable: false, removeCostBtn: false});
    var index = this.costs.length-1;
  }

  removeCostRow($index, data) {
    this.costs.splice($index, 1)
    if(data.credit == '') {
      this.calculateTotal(data.debit ,$index,'debit')
    } else {
      this.calculateTotal(data.credit ,$index, 'credit')
    }
  }

