import angular from 'angular';
import uiRouter from 'angular-ui-router';
import renewPasswordComponent from './renewPassword.component';

let renewPasswordModule = angular.module('renewPassword', [
  uiRouter
])

.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";
  $stateProvider
    .state('renewPassword', {
      url: '/renewPassword/:params1/:params2',
      component: 'renewPassword'
    });
})

.component('renewPassword', renewPasswordComponent)

.name;

export default renewPasswordModule;



