class ProfileController {
  constructor(LoginSrv, UserSrv, ToastSrv, constants, moment, blockUI, blockUIConfig, $window, $rootScope) {
    'ngInject';
    this.name = 'profile';
    this.edituser = false;
    this.LoginSrv = LoginSrv;
    this.UserSrv = UserSrv;
    this.ToastSrv = ToastSrv;
    this.moment = moment;
    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.$window=$window;
    this.constants = constants;
    this.photoChangedVal = false;
    this.$rootScope = $rootScope;
    this.options = {
      // minDate: new Date(),
      maxDate: new Date(),
      showWeeks: false
    };

    this.intialServices();
  }

  intialServices() {
    this.add = '';
    this.LoginSrv.getUser((resp) => {
      this.user = resp;
      this.UserSrv.getUserData(this.user.user, (resp) => {
        this.profile = resp;
        if(this.profile.photo == null) {
          this.photoChangedVal = false;
          this.photoChanged = '../../assets/img/default_image.jpg' ;       
        } else {
          this.photoChangedVal = true;
          this.photoChanged = this.profile.photo;
        }
        if (this.profile.date_of_birth) {
          this.profile.date_of_birth = new Date(this.profile.date_of_birth);
        }
        if(this.profile.address_details != null) {
          this.add += this.profile.address_details.address_line1? (this.profile.address_details.address_line1 + ', ') : '';
          this.add += this.profile.address_details.address_line2? (this.profile.address_details.address_line2 + ', ') : '';
          this.add += this.profile.address_details.city_details? (this.profile.address_details.city_details.city + ', ') : '';
          this.add += this.profile.address_details.pincode? (this.profile.address_details.pincode) : '';
        }
      })
    })
    this.UserSrv.getAllCities((data) => {
      this.cities = data;
    })
    this.clearFile();
  }


  saveEdits() {
    var data = this.profile;
    for (var key in data) { if (data[key] === null) { delete data[key]; }}
    this.blockUI.start();
    delete data.photo;
    this.UserSrv.editUser(data, (resp) => {
      if (resp.success) {
        this.ToastSrv.notify('Profile has been Updated successfully.', 'success')
        this.intialServices();
        this.blockUI.stop();
        this.edituser = !this.edituser;
      } else { this.ToastSrv.notify('Something went Wrong Please try again !', 'error') }
    })
  }

  open1() { //open datetime picker inside modal                 
    this.popup1 = true;
  };

  editprofileView() {
    this.edituser = !this.edituser;
    this.intialServices();
  }

  fileName() { //naming function onselect the file 
    this.PhotoFileName = this.PhotoFile.name;
  }

  clearFile() { //clear file name && file
    this.PhotoFile = '';
    this.PhotoFileName = '';
  }

  UploadImageFile(loc) {
    if(loc == 'upload') {
      var data = this.profile;
      data.photo = this.PhotoFile;
    } else {
      var data = this.profile;
      data.photo = this.constants.EXPORT_URL+'/documents/user_images/default_image.jpg';
    }
    this.blockUI.start();
    this.UserSrv.UploadImageFileSrv(data, (resp) => {
      if (resp.success || resp.status < 300) {
        this.intialServices();
        this.ToastSrv.notify('Profile image changed successfully.', 'success')
        this.blockUI.stop();
        this.$window.location.reload();
      }
    })
  }

  removePhoto() {
    this.photoData = {
      photo : null
    }
    this.UserSrv.patchProfile(this.profile.id, this.photoData,  (resp) => {
      this.intialServices();
      this.ToastSrv.notify('Profile image removed successfully.', 'success')  
      this.$rootScope.photo = '../../assets/img/default_image.jpg';
    })
  } 

}


export default ProfileController;



