  /**
   * Function to set status lists to a variable for displaying statuses in the record listing
   */
  setStatusList() {
    this.status_list = "";
    var cnt = 0;
    if (this.record.status_details) {
      var l = this.record.status_details.length;
      for (var status of this.record.status_details) {
        cnt += 1;
        var j = status.status;
        this.status_list = (cnt == l) ? (this.status_list + j) : (this.status_list + j + ', ');
      }
    }
  } // setStatusList ends

  /**
   * Function to get record of logged in client
   */
  getRecord() {
    this.nameList = [];
    this.RecordSrv.singleRecord(this.client.record_id, (resp) => {
      this.add = '';
      this.record = resp.data;
      this.$rootScope.record = this.record;
      if (this.record.address_details) {
        // this.add += this.record.address_details.address_line1 ? this.record.address_details.address_line1 : '';
        // this.add += this.record.address_details.pincode ? ',' + this.record.address_details.pincode : '';
        this.add1 = this.record.address_details.address_line1 ? this.record.address_details.address_line1 : '';
        this.pincode = this.record.address_details.pincode ? this.record.address_details.pincode : ''
        if(this.record.address_details.city_details) {
          this.city = this.record.address_details.city_details.city+', '+this.record.address_details.city_details.state;
        }
      }
      this.tax = this.$rootScope.record.tax_address ? this.$rootScope.record.tax_address : ''
      for(var i=0;i<this.$rootScope.record.prospect_details.length;i++) {
        if(this.$rootScope.record.prospect_details[i].profession_details && this.$rootScope.record.prospect_details[i].profession_details.profession == 'Prospect') {
          this.nameList.push(this.$rootScope.record.prospect_details[i].name);
        }
      }
      this.nameSepert = this.nameList.join();
      this.createProspectList(this.record.prospect_details);
      this.displayProspectData(this.record.prospect_details)
      this.setStatusList();
      this.changing();
      this.getNotes();
      this.checked = this.record.tax ? true : false;
      window.localStorage.setItem('refresh', 'false')
      

    })
  } // getRecord ends

  getNotes(){
    this.RecordSrv.getAllNotes(this.record.id, (resp) => {
      this.$rootScope.notesList = resp.data;
    })
  }

  displayProspectData(data) {
    this.list_data = [];
    for (var i of data) {
      if (i.is_contact) {
        this.list_data.push(i)
      } else if (i.profession_details) {
        if (i.profession_details.profession == 'Prospect') {
          this.list_data.push(i)
        }
      }
    }
    this.$rootScope.list_data = this.list_data;
  }

  createProspectList(prospect_details) {
    var list = [];
    for (var i of prospect_details) {
      var temp_list = {
        id: i.id,
        type: i.profession,
        company: i.company_details ? i.company_details.company : '',
        person: i.name,
        phone: i.phone,
        email: i.email,
        fax: i.fax,
        phone: i.phone,
      }
      i.is_contact ? list.push(temp_list) : '';
    }
    this.$rootScope.prospect = list;
  }

  /**
  * Function to get and list out prospect type list
  */
  getProspectTypes() {
    this.ContactSrv.getProfession((resp) => {
      for (var i of resp.data) {
        if (i.profession == 'Banker' || i.profession == 'Attorney' || i.profession == 'Prospect') { this.typeList.push(i); }
      }
    })
    this.$rootScope.typeList = this.typeList;
  } // type list function ends

  /**
   * Function to get company lists
   * @param {profession} type 
   * @param {company} val 
   */
  geCompanyList(type, val) {
    if (type == undefined || type == '') {
      this.ToastSrv.notify('Please select Type', 'error')
    } else {
      return this.$http.get(this.constants.BASE_URL + '/contacts/persons/', {
        params: {
          company: val,
          profession: type.profession
        }
      }).then(function (response) {
        return response.data.map(function (item) {
          return item.company_details;
        });
      });
    }
  };

  fileName() { //naming function onselect the file
    this.PhotoFileName = this.PhotoFile.name;
  }

  clearFile() { //clear file name && file
    this.PhotoFile = '';
    this.PhotoFileName = '';
  }

  changing() {
    if (this.checked) {
      this.$rootScope.record.tax_address = (this.$rootScope.record.address_details.address_line1 + ', ' + this.$rootScope.record.address_details.address_line2 + ', ' + this.$rootScope.record.address_details.pincode)
    }
  }

  getPersonList(prof, prof_name, val) {
    if (prof == undefined || prof == '') {
      this.ToastSrv.notify('Please select Type', 'error')
    } else if (prof_name == undefined || prof_name == '') {
      this.ToastSrv.notify('Please select Name', 'error')
    } else {
      return this.$http.get(this.constants.BASE_URL + '/contacts/persons/', {
        params: {
          name: val,
          company: prof_name.company,
          profession: prof.profession
        }
      }).then(function (response) {
        return response.data.map(function (item) {
          return item;
        });
      });
    }
  }

  saveUpdateProperty() {
    this.record.prospect = this.tempProspect;
    delete this.record.prospect;
    delete this.record.attachment_details;
    delete this.record.information_details;
    delete this.record.event_details;
    delete this.record.prospect_details;
    this.record.tax = this.checked;
    this.RecordSrv.editRecord(this.record, this.record.id, (resp) => {
      this.getRecord()
      this.view = true;
      this.blockUI.stop();
      this.ToastSrv.notify('Record Updated Successfully', 'success')
    })
  }

  createContact(id, prospect) {
    this.listContact = [];
    var contactData = {
      company: id,
      name: prospect.person,
      profession: prospect.type,
      phone: prospect.phone,
      contact_address: '',
      email: prospect.email,
      fax: prospect.fax
    }
    this.listContact.push(contactData);
    this.ContactSrv.addContactSrv(this.listContact, (res) => {
      this.tempProspect.push(res.data.data[0].id)
    })
  }

  checkUpdate(contact) {
    var data = {
      phone: contact.phone,
      email: contact.email,
      fax: contact.fax,
    }
    this.ContactSrv.updateContacts(data, contact.id, (resp) => {
    })
  }

