  //--------------function for opening imfomation on property modal----------//
  openModalinformation() {
    this.$uibModal.open({
      templateUrl: 'informationModal.html',
      size: 'lg',
      controller: ['$uibModalInstance', '$rootScope', '$scope', function ($uibModalInstance, $rootScope) {
        this.weekDays = [
          { id: 'MO', day: 'Monday' },
          { id: 'TU', day: 'Tuesday' },
          { id: 'WE', day: 'Wednesday' },
          { id: 'TH', day: 'Thursday' },
          { id: 'FR', day: 'Friday' },
          { id: 'SA', day: 'Saturday' },
          { id: 'SU', day: 'Sunday' }
        ];
        $rootScope.RecordSrv.getInfoDetails($rootScope.rId, (resp) => {
          if(resp.data.length != 0) {
            this.information_details = resp.data[0];
            this.inId = resp.data[0].id;
          } else { 
            this.information_details = {};
          }
        })
        this.addInfo = function (action) {
          this.information_details['property'] = $rootScope.rId;
          this.infoDetails = this.information_details;
         
          if(action == 'add') {
            $rootScope.RecordSrv.addInformatn(this.infoDetails, (resp) => {
              
              $rootScope.ToastSrv.notify("Information added Successfully.!", 'success');
              $uibModalInstance.close();
            })
          } else {
            $rootScope.RecordSrv.updInformatn(this.information_details, this.information_details.id, (resp) => {
              
              $rootScope.ToastSrv.notify("Information Updated Successfully.!", 'success');
              $uibModalInstance.close();
            })
          }
          $uibModalInstance.close();
        }
        this.cancel = function () {
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vn'
    })
  }
  //------------information on property modal ends--------------------------//

