          $rootScope.blockUI.start();
          $rootScope.file.forEach(element => {
            element['uploaded_by'] = $rootScope.clientId;
            $rootScope.RecordSrv.addAttachments(element, $rootScope.record.id, (resp) => {
              $rootScope.file = '';
              if (resp.success) {
                $rootScope.getRecord();
                $rootScope.blockUI.stop();
              }
            })
          })
