  openAddNoteModal() {
    this.$uibModal.open({
      templateUrl: 'addNoteModal.html',
      backdrop: 'static',
      size: 'lg',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        var id = $rootScope.record.client_details.id;
        // if (this.recordNote.length > 0) {
        //   this.choices = this.recordNote;

        // } else {
          this.choices = [{}]
        // }

        this.addNewChoice = function () {
          this.choices.push({});
          
        };

        this.removeNewChoice = function (index) {
          
          var newItemNo = this.choices.length - 1;
          if (newItemNo !== 0) {
            this.choices.splice(index, 1);
          } else if (newItemNo == 0) {
            this.choices[0] = {};
            this.btndisbled = true;
          }
        };

        this.showAddChoice = function (choice) {
          return choice === this.choices[this.choices.length - 1];
        };

        this.addNote = function () {
          this.fullNoteList = [];
          // if($rootScope.notesList.length > 0) {
          //   for(var i=0;i<$rootScope.notesList.length;i++) {
          //     this.fullNoteList.push($rootScope.notesList[i]);
          //   }
          // }
          Object.keys(this.choices).forEach((key) => (this.choices[key] == null) && delete this.choices[key]);
          for(var i=0;i<this.choices.length;i++) {
            this.choices[i]['property'] = $rootScope.rId;
            this.choices[i]['added_by'] = $rootScope.userId;
            this.fullNoteList.push(this.choices[i])
          }
          // var data = {
          //   note_details: this.fullNoteList
          // }
          $rootScope.RecordSrv.addNotes(this.fullNoteList, (resp) => {
            $uibModalInstance.close();
            $rootScope.getNotes()
            $rootScope.ToastSrv.notify('Note has been added successfully.', 'success')
          })
        }
        this.close = function () {
          $rootScope.getNotes()
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vy'
    })
  }

  openViewNoteModal() {
    this.$uibModal.open({
      templateUrl: 'viewNoteModal.html',
      backdrop: 'static',
      size: 'lg',
      controller: ['$uibModalInstance', '$rootScope', function ($uibModalInstance, $rootScope) {
        this.recordNote = $rootScope.notesList;
       

        this.updateNote = function(id, desc) {
          var data = {
            description : desc
          }
          $rootScope.RecordSrv.updateNote(data, id, (resp) => {
            $rootScope.ToastSrv.notify('Note updated Successfully', 'success')
            $rootScope.getNotes();
          })
        }

        this.deleteNote = function(id, index) {
          $rootScope.RecordSrv.deleteNote(id, (resp) => {
           
            this.recordNote.splice(index,1);
            if(this.recordNote.length == 0) {
              $uibModalInstance.close();
            }
            $rootScope.getNotes()
            $rootScope.ToastSrv.notify('Note has been deleted successfully.', 'success')
          })
        }

        this.close = function () {
          $rootScope.getNotes()
          $uibModalInstance.close();
        }
      }],
      controllerAs: 'vv'
    })
  }



  logout() {
    window.localStorage.setItem('refresh', "false")
    window.localStorage.clear();
    this.LoginSrv.logout();
  }


