import angular from 'angular';
import uiRouter from 'angular-ui-router';
import uiBootstrap from 'angular-ui-bootstrap';
import Components from './components/components';
import AppComponent from './app.component';
import Inspinia from './directives/directives';
import Appservices from './services/services';
import AppConst from './appConst';
import AuthInterceptorFactory from './authInterceptors';
import calendarConfig from 'angular-bootstrap-calendar';
import 'moment';
import 'angular-toastr/dist/angular-toastr.css';
import 'ui-select/dist/select.css';
import 'bootstrap-ui-datetime-picker/dist/datetime-picker.min.js';
import 'jquery';
import 'animate';
import 'font-awesome/css/font-awesome.min.css';
import 'normalize.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'animate.css/animate.css';
import 'metismenu';
import '../inspinia';
import '../assets/css/style.css';
import '../assets/css/jerin.css';
import 'ui-select/dist/select.css';
import 'angularjs-datepicker';
import 'angularjs-datepicker/dist/angular-datepicker.css';
import 'ng-file-upload';
import uiBootstrapDatePicker from 'angular-bootstrap-datetimepicker';
import Chart from 'angular-chart.js';
import 'angular-bootstrap-multiselect';
import 'bootstrap-submenu-hover';
import 'angularjs-dropdown-multiselect';
import toastr from 'angular-toastr'
import ngRoute from 'angular-route'
import uiSelect from 'ui-select'
import ngSanitize from 'angular-sanitize';
import uiBootstrapDatetimePicker from 'bootstrap-ui-datetime-picker';
import 'checklist-model';
import 'pretty-checkbox/dist/pretty-checkbox.min.css';
import BlockUi from 'angular-block-ui';
import 'angular-block-ui/dist/angular-block-ui.min.css';
import 'angular-timeago';
// import 'ng-tags-input';
// import 'ng-tags-input/build/ng-tags-input.css';
// import 'ng-tags-input/build/ng-tags-input.bootstrap.css';
// import 'angular-xeditable/dist/css/xeditable.css'
// import 'angular-xeditable/dist/js/xeditable'
// import 'ng-sticky-header/dist/sticky-header';


angular.module('carleton', [
  uiRouter,
  Components,
  Inspinia,
  uiBootstrap,
  Appservices,
  AppConst,
  '720kb.datepicker',
  calendarConfig,
  uiBootstrapDatePicker,
  'ngFileUpload',
  Chart,
  toastr,
  ngRoute,
  ngSanitize,
  uiSelect,
  uiBootstrapDatetimePicker,
  BlockUi,
  'btorfs.multiselect',
  'angularjs-dropdown-multiselect',
  'checklist-model',
  'yaru22.angular-timeago',
  // 'xeditable',
  // 'sticky-header'
  // 'mwl.calendar',
  // 'ngTagsInput',



])

  .constant('_', window._)
  .factory('AuthInterceptor', AuthInterceptorFactory)
  .config(($locationProvider, $stateProvider, $httpProvider, $urlRouterProvider ,blockUIConfig) => {
    "ngInject";
    blockUIConfig.autoBlock = false;
    $httpProvider.interceptors.push('AuthInterceptor');

    $locationProvider.html5Mode(true).hashPrefix('!');
    $stateProvider
      .state('carleton', {
        url: '/carleton',
        component: 'carleton',
      })
    if (window.localStorage.getItem('carleton.auth.token')) {
      if(window.localStorage.getItem('client')) {
        $urlRouterProvider.otherwise('clientportal')
      } else {$urlRouterProvider.otherwise('carleton/dashboard')}
    }
    else {
      $urlRouterProvider.otherwise('/login')
    }
  })

  .run(($rootScope, $state, $stateParams, $location, toastrConfig, $transitions) => {
    "ngInject";
    $rootScope.$state = $state;
    $rootScope.$stateParams = $stateParams;
    Date.prototype.getUnixTime = function () { return this.getTime() / 1000 | 0 }
    if (!Date.now) Date.now = function () { return new Date() }
    Date.time = function () { return Date.now().getUnixTime() }

    //use if $transition fails for remain in login if no token else to dasboard


    // var url = $location.path().split('/')[1];
    // if (!(window.localStorage.getItem('carleton.auth.token'))) {
    // if(url=='renewPassword'){
    // $location.path($location.path());
    // }
    // $location.path('/login')
    // else{}
    // }

    $transitions.onEnter({ entering: "clientportal" }, function ($transition) {
      if (!(window.localStorage.getItem('carleton.auth.token'))) {
        $location.path('/login');
      }
      else if ((window.localStorage.getItem('carleton.auth.token'))) {
        $state.target('clientportal');
      }

    });

    if(window.localStorage.newTab){
      var newTab = JSON.parse(window.localStorage.newTab);
      window.localStorage.removeItem("newTab");
      $state.go(newTab.state, newTab.params);
      $rootScope.$on('$locationChangeStart', function (event) {
        event.preventDefault();
     });
   }

    //use  for remain in login if no token else go to dasboard
    $transitions.onEnter({ entering: "carleton" }, function ($transition) {
      if (!(window.localStorage.getItem('carleton.auth.token'))) {
        $location.path('/login');
      }
      else if ((window.localStorage.getItem('carleton.auth.token'))) {
        $state.target('carleton.dasboard');
      }

    });
    $transitions.onEnter({ entering: "renewPassword" }, function ($transition) {
      $state.target('renewPassword');

    });

    $transitions.onEnter({ entering: "login" }, function ($transition) {
      if (!(window.localStorage.getItem('carleton.auth.token'))) {
        $location.path('/login');
      }
      else if ((window.localStorage.getItem('carleton.auth.token'))) {
        if(window.localStorage.getItem('client')) {
          $location.path('clientportal')
        } else {$location.path('carleton/dashboard')}
        // $location.path('carleton/dasboard');
      }
    });

  })

  .component('carleton', AppComponent);



