import angular from 'angular';
import uiRouter from 'angular-ui-router';
import addRecordsComponent from './addRecords.component';

let addRecordsModule = angular.module('addRecords', [
  uiRouter
])

.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";
  $stateProvider
    .state('carleton.addRecords', {
      url: '/addRecords',
      component: 'addRecords'
    });
})

.component('addRecords', addRecordsComponent)

.name;

export default addRecordsModule;



