class DashboardController {
  constructor(dashboardSrv, moment, blockUI, blockUIConfig, $state) {
    "ngInject";
    this.name = 'dashboard';
    this.dashboardSrv = dashboardSrv;
    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.myBlockUI = this.blockUI.instances.get('myBlockUI');
    this.blockUIConfig.resetOnException = true;
    this.moment = moment
    this.$state = $state;
    this.tab = 1;
    this.quantity = 12;
    this.labels = ["Follow up", "New Record", "Returned Mail", "Archive"]; //  data for chart
    this.colors = ['#b5bccf', '#abded4', '#dfdfdf', '#ebf2f5'];
    this.series = ['Follow up', 'New Record', 'Returned Mail', 'Archive'];
    this.loggedUser = JSON.parse(localStorage.getItem('user'));
    this.chartOptions = {
      legend: {
        display: true,
        position: 'right'
      }
    };

    this.range = [];
    this.emptyChart = false;

    this.options = { //datepicker option
      maxDate: new Date(),
      showWeeks: false
    };

    // this.maxDate1 = new Date().toDateString();
    // this.maxDate = new Date().toDateString();


    this.intialServices();

  }
  intialServices() {
    this.myBlockUI.start();
    this.dashboardSrv.recentActivitySrv((resp) => {
      this.recentData = resp;
      this.myBlockUI.stop();
    })
    this.filterRange();

    // var formatedRange = [];
    // formatedRange.q = '';
    // formatedRange.from_date = '';
    // formatedRange.to_date = '';
    // this.dashboardSrv.filterRangeSrv(formatedRange, (resp) => { // chart data initially Loading
    //   this.myBlockUI.stop();
    //   // this.data = resp.data;
    //   this.chartData = resp.data.results.record_count;
    //   if ((/[^0]/).exec(this.chartData.join(""))) {
    //     this.emptyChart = false;
    //   } else {
    //     this.emptyChart = true;
    //   }
    // })
  }

  sortData(status, tab) {
    this.myBlockUI.start();
    this.tab = tab;
    this.status = status;
    this.respdata = false;
    this.dashboardSrv.SortPropertySrv(status, (resp) => {
      this.myBlockUI.stop();
      this.data = resp.data.results.data;
      this.respdata = true;
    })
  }

  filterRange() {
    var formatedRange = [];
    this.myBlockUI.start();

    if (this.range.from_date && this.range.to_date) {
      formatedRange.q = '';
      formatedRange.from_date = this.moment(this.range.from_date).format('YYYY-MM-DD');
      formatedRange.to_date = this.moment(this.range.to_date).format('YYYY-MM-DD');
    } else if (this.range.from_date) {
      formatedRange.q = '';
      formatedRange.to_date = this.moment().format('YYYY-MM-DD');
      formatedRange.from_date = this.moment(this.range.from_date).format('YYYY-MM-DD');
    } else if (this.range.to_date) {
      formatedRange.q = '';
      formatedRange.from_date = '';
      formatedRange.to_date = this.moment(this.range.to_date).format('YYYY-MM-DD');
    } else {
      formatedRange.q = '';
      formatedRange.from_date = '';
      formatedRange.to_date = '';


    }
    this.dashboardSrv.filterRangeSrv(formatedRange, (resp) => {
      this.myBlockUI.stop();
      this.chartData = resp.data.results.record_count;
      if ((/[^0]/).exec(this.chartData.join(""))) {
        this.emptyChart = false;
      } else {
        this.emptyChart = true;
      }
    })

  }


  open1() { //open datetime picker inside modal
    this.popup1 = true;
  };

  open2() { //open datetime picker inside modal
    this.popup2 = true;
  };
  clearFilter() {
    if (this.range.to_date) this.range.to_date = '';
    if (this.range.from_date) this.range.from_date = '';
    this.sortData('Followed Up', 1);
    this.intialServices();
    // this.maxDate1 = new Date().toDateString();
    // this.maxDate = new Date().toDateString();
  }

  navigateToView(id) {
    this.$state.go('carleton.records', {
      'id': id
    })
  }


}

export default DashboardController;



