import angular from 'angular';
import UserSrv from './user.srv';
import ContactSrv from './contact.srv';
import AuthService from './authSrv';
import LoginSrv from './login.srv';
import ToastSrv from './toast.srv'
import RecordSrv from './record.srv';
import CalendarSrv from './calendar.srv';
import ImportSrv from './import.srv';
import ClientSrv from './client.srv';
import dashboardSrv from './dashboard.srv'
import ReportSrv from './report.srv'
import TemplateSrv from './template.srv'




const serviceModule = angular.module('service', [])

.service('CalendarSrv',CalendarSrv)
.service('ToastSrv', ToastSrv)
.service('UserSrv', UserSrv)
.service('LoginSrv', LoginSrv)
.service('RecordSrv', RecordSrv)
.service('ContactSrv', ContactSrv)
.service('AuthService', AuthService)
.service('ImportSrv', ImportSrv)
.service('ClientSrv', ClientSrv)
.service('dashboardSrv', dashboardSrv)
.service('ReportSrv',ReportSrv)
.service('TemplateSrv',TemplateSrv)




.name;

export default serviceModule;



