import angular from 'angular';
import uiRouter from 'angular-ui-router';
import clientportalComponent from './clientportal.component';

let clientportalModule = angular.module('clientportal', [
  uiRouter
])

.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";
  $stateProvider
    .state('clientportal', {
      url: '/clientportal',
      component: 'clientportal'
    });
})

.component('clientportal', clientportalComponent)

.name;

export default clientportalModule;



