class dashboardSrv {
  constructor($http, $state, constants, AuthService, Upload, ToastSrv) {
    'ngInject';
    this.$http = $http;
    this.constants = constants;
    this.state = $state;
    this.AuthService = AuthService;
    this.ToastSrv = ToastSrv;
    this.Upload = Upload;
  }
  SortPropertySrv(key, callback) {
    var url = this.constants.BASE_URL + '/properties/records/?stat=' + key;
    this.$http.get(url).then((resp) => {
      callback(resp);
    })
  }

  filterRangeSrv(range, callback) {
    var fdate = range.from_date;
    var tdate = range.to_date;
    var tdy = range.q
    var url = this.constants.BASE_URL + '/properties/records/?from_date=' + fdate + '&to_date=' + tdate + '&q=' + tdy;
    this.$http.get(url).then((resp) => {
      callback(resp);
    })
  }

  notificationSrv(callback) {
    var url = this.constants.BASE_URL + '/calendar/events/?q=today';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }

  recentActivitySrv(callback) {
    var url = this.constants.BASE_URL + '/notifications/list/?q=past7';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    }).catch(() => {
      this.ToastSrv.notify('Connectivity Error...!', 'error')
    })
  }


  demoSearch(query) {
    const url = this.constants.BASE_URL + '/files/import/'
    return this.$http.get(url, {
      params: {
        str: query,
      }
    }).then((resp) => {
      return (resp.data)
    })
  }


  toDoSrv(callback) {
    var url = this.constants.BASE_URL + '/calendar/events/?q=month';
    this.$http.get(url).then((resp) => {
      callback(resp.data);
    })
  }

  updatetoDoSrv(id, data, callback) {
    var url = this.constants.BASE_URL + '/calendar/events/' + id + '/';
    this.$http.patch(url, data).then((resp) => {
      callback(resp.data);

    })
  }

}
export default dashboardSrv;



