class ReportsController {
  constructor(ReportSrv, ToastSrv, blockUI, blockUIConfig,$state) {
    'ngInject';
    this.name = 'reports';
    this.ReportSrv = ReportSrv;
    this.ToastSrv = ToastSrv;
    this.$state=$state;

    this.blockUI = blockUI;
    this.blockUIConfig = blockUIConfig;
    this.blockUIConfig.autoBlock = false;
    this.myBlockUI = this.blockUI.instances.get('myBlockUI');
    this.blockUIConfig.resetOnException = true;

    this.tab = true;
    this.maxDate = new Date().toDateString();
  }

  getStatus(val) { //search in status 
    return this.ReportSrv.getStatusSrv(val).then((result) => {
      this.searchResult = result;
      return result.map(function (item) {
        return item;
      });
    })
  }

  getExpense(val) { //search in expense 
    return this.ReportSrv.getExpenseSrv(val).then((result) => {
      this.searchResult = result;
      return result.map(function (item) {
        return item;
      });
    })
  }

  dateFilterReport() {
    var frmDate = this.data.fromdate ? this.data.fromdate.split("/").reverse().join("-") : '';
    var toDat = this.data.todate ? this.data.todate.split("/").reverse().join("-") : '';
    this.data['frmDate'] = frmDate;
    this.data['toDat'] = toDat;
    this.myBlockUI.start();
    this.ReportSrv.getreportSrv(this.data,(resp) => {
      this.reportdata=resp.data.results.report_count;
      this.myBlockUI.stop();

    })
  }

   getRecord(val) { //search in records 
    return this.ReportSrv.getRecordSrv(val).then((result) => {
      this.searchResult = result.data.results.data;
      return result.data.results.data.map(function (item) {
        return item;
      });
    })
  }

  getRecordReport() { //reports in records 
    this.myBlockUI.start();
    this.ReportSrv.getRecordReportSrv(this.rdata.record_no,(resp) => {
      this.myBlockUI.stop();

      this.recordReportdata=resp.data.results.report_count;
    })
  }

  clearReport(){
    this.data={};
    this.reportdata='';
    this.dropName = 'Choose Date'
  }

  clearRecordReport(){
    this.recordReportdata={};
    this.rdata='';
    this.loadingRecord='';
    this.noRecord=false;
  }

  // deleteFromdate() { //open date picker 1                 
  //   delete this.data.fromdate
  // }

  // deleteTodate() { //open date picker 2                 
  //   delete this.data.todate
  // }


  // onSearchUser(searchResult) { //on select searchUsers 
  //   this.users = [searchResult];
  // }

}

export default ReportsController;



